<template>
  <div class="admin-login flex flex-col items-center justify-center min-h-screen">
    <div class="bg-white p-8 rounded shadow-md w-full max-w-sm">
      <h2 class="text-2xl font-bold mb-4 text-center">Admin Login</h2>
      <form @submit.prevent="login">
        <div class="mb-4">
          <label class="block mb-1">Username or Email</label>
          <input v-model="identifier" type="text" class="w-full border rounded px-3 py-2" required />
        </div>
        <div class="mb-4">
          <label class="block mb-1">Password</label>
          <input v-model="password" type="password" class="w-full border rounded px-3 py-2" required />
        </div>
        <div v-if="error" class="text-red-500 mb-2">{{ error }}</div>
        <button type="submit" class="btn btn-primary w-full">Login</button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      identifier: '',
      password: '',
      error: ''
    };
  },
  methods: {
    async login() {
      this.error = '';
      const res = await fetch('http://192.168.1.113/expenseNote/api/api.php?action=admin_login', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ username: this.identifier, email: this.identifier, password: this.password })
      });
      const data = await res.json();
      if (data.success) {
        localStorage.setItem('admin_token', data.token);
        this.$router.push('/admin/dashboard');
      } else {
        this.error = data.message || 'Login failed';
      }
    }
  }
};
</script>

<style scoped>
.admin-login {
  background: #f3f4f6;
}
</style>
