<template>
  <!-- Feedback Modal -->
  <div v-if="show" class="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center z-50 p-4" @click.self="$emit('close')">
    <div class="glass-modal max-w-md w-full max-h-[90vh] overflow-y-auto animate-modalSlideIn">
      <div class="flex justify-between items-center mb-6">
        <h2 class="text-2xl font-bold bg-gradient-to-r from-orange-500 to-red-500 bg-clip-text text-transparent">📝 Send Feedback</h2>
        <button @click="$emit('close')" class="glass-button p-2 rounded-lg hover:scale-110 transition-all group">
          <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 text-gray-500 group-hover:text-red-500 transition-colors" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M6 18L18 6M6 6l12 12" />
          </svg>
        </button>
      </div>

      <form @submit.prevent="submitFeedback" class="space-y-4">
        <!-- Email Field -->
        <div>
          <label class="block text-sm font-medium text-gray-600 dark:text-gray-300 mb-2">
            Your Email (optional)
          </label>
          <input 
            v-model="feedbackForm.email" 
            type="email" 
            placeholder="your.email@example.com"
            class="w-full px-4 py-3 rounded-lg glass-item border border-gray-300 dark:border-gray-600 focus:border-orange-500 focus:outline-none transition-all duration-300 text-gray-800 dark:text-white placeholder-gray-500"
          />
        </div>

        <!-- Rating Field -->
        <div>
          <label class="block text-sm font-medium text-gray-600 dark:text-gray-300 mb-2">
            Rate your experience *
          </label>
          <div class="flex space-x-2">
            <button 
              v-for="star in 5" 
              :key="star"
              type="button"
              @click="feedbackForm.rating = star"
              class="p-2 hover:scale-110 transition-all duration-200"
              :class="star <= feedbackForm.rating ? 'text-yellow-400' : 'text-gray-400'"
            >
              <svg class="w-8 h-8" fill="currentColor" viewBox="0 0 24 24">
                <path d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z"/>
              </svg>
            </button>
          </div>
          <p class="text-xs text-gray-500 mt-1">
            {{ getRatingText() }}
          </p>
        </div>

        <!-- Feedback Category -->
        <div>
          <label class="block text-sm font-medium text-gray-600 dark:text-gray-300 mb-2">
            Feedback Type *
          </label>
          <select 
            v-model="feedbackForm.category" 
            required
            class="w-full px-4 py-3 rounded-lg glass-item border border-gray-300 dark:border-gray-600 focus:border-orange-500 focus:outline-none transition-all duration-300 text-gray-800 dark:text-white"
          >
            <option value="">Select category...</option>
            <option value="bug">🐛 Bug Report</option>
            <option value="feature">💡 Feature Request</option>
            <option value="ui">🎨 UI/UX Feedback</option>
            <option value="performance">⚡ Performance Issue</option>
            <option value="general">💬 General Feedback</option>
            <option value="compliment">👏 Compliment</option>
          </select>
        </div>

        <!-- Feedback Text -->
        <div>
          <label class="block text-sm font-medium text-gray-600 dark:text-gray-300 mb-2">
            Your Message *
          </label>
          <textarea 
            v-model="feedbackForm.message" 
            required
            rows="4"
            placeholder="Tell us about your experience, suggestions, or any issues you've encountered..."
            class="w-full px-4 py-3 rounded-lg glass-item border border-gray-300 dark:border-gray-600 focus:border-orange-500 focus:outline-none transition-all duration-300 text-gray-800 dark:text-white placeholder-gray-500 resize-none"
          ></textarea>
          <p class="text-xs text-gray-500 mt-1">
            {{ feedbackForm.message.length }}/500 characters
          </p>
        </div>

        <!-- Include System Info -->
        <div class="flex items-center space-x-2">
          <input 
            v-model="feedbackForm.includeSystemInfo" 
            type="checkbox" 
            id="systemInfo"
            class="w-4 h-4 text-orange-500 border-gray-300 rounded focus:ring-orange-500"
          />
          <label for="systemInfo" class="text-sm text-gray-600 dark:text-gray-300">
            Include system information (browser, screen size, etc.)
          </label>
        </div>

        <!-- Action Buttons -->
        <div class="flex space-x-3 pt-4">
          <button 
            type="button" 
            @click="$emit('close')"
            class="flex-1 px-4 py-3 bg-gray-500 hover:bg-gray-600 text-white rounded-lg transition-all duration-300 font-medium"
          >
            Cancel
          </button>
          <button 
            type="submit" 
            :disabled="!isFormValid || isSubmitting"
            class="flex-1 px-4 py-3 bg-gradient-to-r from-orange-500 to-red-500 hover:from-orange-600 hover:to-red-600 disabled:from-gray-400 disabled:to-gray-500 text-white rounded-lg transition-all duration-300 font-medium flex items-center justify-center"
          >
            <svg v-if="isSubmitting" class="animate-spin -ml-1 mr-2 h-4 w-4 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
              <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
              <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
            </svg>
            {{ isSubmitting ? 'Sending...' : 'Send Feedback' }}
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { ref, reactive, computed } from 'vue';
import Swal from 'sweetalert2';

export default {
  name: 'FeedbackModal',
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  emits: ['close'],
  setup(props, { emit }) {
    const isSubmitting = ref(false);
    
    const feedbackForm = reactive({
      email: '',
      rating: 0,
      category: '',
      message: '',
      includeSystemInfo: true
    });

    const isFormValid = computed(() => {
      return feedbackForm.rating > 0 && 
             feedbackForm.category !== '' && 
             feedbackForm.message.trim().length >= 10 &&
             feedbackForm.message.length <= 500;
    });

    const getRatingText = () => {
      const texts = {
        0: 'Please select a rating',
        1: 'Poor - Needs significant improvement',
        2: 'Fair - Has some issues',
        3: 'Good - Generally satisfactory',
        4: 'Very Good - Minor improvements needed',
        5: 'Excellent - Exceeds expectations'
      };
      return texts[feedbackForm.rating] || '';
    };

    const getSystemInfo = () => {
      if (!feedbackForm.includeSystemInfo) return '';
      
      return `
System Information:
- Browser: ${navigator.userAgent}
- Screen Resolution: ${window.screen.width}x${window.screen.height}
- Viewport: ${window.innerWidth}x${window.innerHeight}
- Platform: ${navigator.platform}
- Language: ${navigator.language}
- Timestamp: ${new Date().toISOString()}
- URL: ${window.location.href}
      `.trim();
    };

    const submitFeedback = async () => {
      if (!isFormValid.value) return;

      isSubmitting.value = true;

      try {
        const feedbackData = {
          email: feedbackForm.email || 'anonymous@user.com',
          rating: feedbackForm.rating,
          category: feedbackForm.category,
          message: feedbackForm.message,
          systemInfo: getSystemInfo()
        };

        const response = await fetch('http://192.168.1.113/expenseNote/api/api.php?action=send_feedback', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(feedbackData)
        });

        if (!response.ok) {
          throw new Error('Failed to send feedback');
        }

        const result = await response.json();
        
        if (result.success) {
          await Swal.fire({
            icon: 'success',
            title: 'Feedback Sent!',
            text: 'Thank you for your feedback! We appreciate your input and will review it carefully.',
            confirmButtonColor: '#f97316'
          });
          
          // Reset form
          Object.assign(feedbackForm, {
            email: '',
            rating: 0,
            category: '',
            message: '',
            includeSystemInfo: true
          });
          
          emit('close');
        } else {
          throw new Error(result.message || 'Failed to send feedback');
        }

      } catch (error) {
        console.error('Error sending feedback:', error);
        await Swal.fire({
          icon: 'error',
          title: 'Failed to Send',
          text: 'Sorry, we couldn\'t send your feedback right now. Please try again later.',
          confirmButtonColor: '#f97316'
        });
      } finally {
        isSubmitting.value = false;
      }
    };

    return {
      feedbackForm,
      isSubmitting,
      isFormValid,
      getRatingText,
      submitFeedback
    };
  }
};
</script>

<style scoped>
.glass-modal {
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 24px;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
}

.glass-button {
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 2px 12px 0 rgba(31, 38, 135, 0.2);
}

.glass-item {
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.08);
}

.animate-modalSlideIn {
  animation: modalSlideIn 0.3s ease-out;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* Dark mode input styling */
.dark .glass-item {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

input:focus, textarea:focus, select:focus {
  box-shadow: 0 0 0 3px rgba(249, 115, 22, 0.1);
}
</style>
