<template>
  <!-- Chart Modal -->
  <div v-if="show" class="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4" @click.self="$emit('close')">
    <div class="glass-card p-6 rounded-lg shadow-xl max-w-lg w-full max-h-[90vh] overflow-y-auto">
      <div class="flex justify-between items-center mb-6">
        <h2 class="text-2xl font-bold theme-text">📊 Analytics Dashboard</h2>
        <button @click="$emit('close')" class="text-gray-400 hover:text-gray-200 transition-colors">
          <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M6 18L18 6M6 6l12 12" />
          </svg>
        </button>
      </div>

      <!-- Chart Content -->
      <div class="space-y-4">
        <!-- Income vs Expenses Progress Bars -->
        <div class="p-4 rounded-lg glass-item">
          <h3 class="text-lg font-semibold theme-text mb-4">Income vs Expenses</h3>
          
          <!-- Income Bar -->
          <div class="mb-4">
            <div class="flex justify-between items-center mb-2">
              <span class="text-sm theme-text">Income</span>
              <span class="text-sm text-green-400">{{ (totalIncome * exchangeRate).toLocaleString() }} MMK</span>
            </div>
            <div class="w-full bg-gray-700 rounded-full h-4">
              <div class="bg-green-400 h-4 rounded-full transition-all duration-500"
                :style="{ width: totalIncome > 0 ? Math.min((totalIncome / Math.max(totalIncome, totalExpenses)) * 100, 100) + '%' : '0%' }">
              </div>
            </div>
          </div>

          <!-- Expenses Bar -->
          <div class="mb-4">
            <div class="flex justify-between items-center mb-2">
              <span class="text-sm theme-text">Expenses</span>
              <span class="text-sm text-red-400">{{ (totalExpenses * exchangeRate).toLocaleString() }} MMK</span>
            </div>
            <div class="w-full bg-gray-700 rounded-full h-4">
              <div class="bg-red-400 h-4 rounded-full transition-all duration-500"
                :style="{ width: totalExpenses > 0 ? Math.min((totalExpenses / Math.max(totalIncome, totalExpenses)) * 100, 100) + '%' : '0%' }">
              </div>
            </div>
          </div>
        </div> 

        <!-- Pie Chart -->
        <div class="p-4 rounded-lg glass-item" v-if="incomeExpenseBreakdown.length > 0">
          <h3 class="text-lg font-semibold theme-text mb-4">Income & Expense Distribution</h3>
          <div class="flex flex-col items-center">
            <!-- Chart.js Pie Chart -->
            <div class="chart-container mb-4" style="position: relative; height: 250px; width: 250px;">
              <canvas ref="pieChartCanvas"></canvas>
            </div>

            <!-- Total Amount Display -->
            <div class="text-center mb-4">
              <p class="text-sm theme-text-muted">Total Transaction Volume</p>
              <p class="text-2xl font-bold theme-text">{{ ((totalIncome + totalExpenses) * exchangeRate).toLocaleString() }} MMK</p>
              <p class="text-sm theme-text-muted">${{ (totalIncome + totalExpenses).toFixed(2) }}</p>
              <div class="flex justify-center space-x-4 mt-2">
                <div class="text-center">
                  <p class="text-xs theme-text-muted">Income</p>
                  <p class="text-sm font-bold text-green-400">{{ (totalIncome * exchangeRate).toLocaleString() }} MMK</p>
                </div>
                <div class="text-center">
                  <p class="text-xs theme-text-muted">Expenses</p>
                  <p class="text-sm font-bold text-red-400">{{ (totalExpenses * exchangeRate).toLocaleString() }} MMK</p>
                </div>
              </div>
            </div>

            <!-- Legend -->
            <div class="w-full space-y-2">
              <div v-for="(category, index) in incomeExpenseBreakdown.slice(0, 8)" :key="category.name"
                class="flex items-center justify-between">
                <div class="flex items-center space-x-2">
                  <div class="w-3 h-3 rounded-full"
                    :style="{ backgroundColor: getChartColors(incomeExpenseBreakdown)[index] }"></div>
                  <span class="text-sm theme-text"
                    :class="category.type === 'income' ? 'text-green-300' : 'text-red-300'">{{ category.originalName || 'Uncategorized' }}</span>
                  <span class="text-xs px-1 py-0.5 rounded"
                    :class="category.type === 'income' ? 'bg-green-600 text-green-100' : 'bg-red-600 text-red-100'">
                    {{ category.type === 'income' ? 'Income' : 'Expense' }}
                  </span>
                </div>
                <div class="text-right">
                  <span class="text-sm theme-text-muted">{{ ((category.amount || 0) * exchangeRate).toLocaleString() }} MMK</span>
                  <span class="text-xs theme-text-muted ml-1">({{ (((category.amount || 0) / (totalIncome + totalExpenses)) * 100).toFixed(1) }}%)</span>
                </div>
              </div>
            </div>
          </div>
        </div>

        <!-- Category Breakdown -->
        <div class="p-4 rounded-lg glass-item" v-if="categoryBreakdown.length > 0">
          <h3 class="text-lg font-semibold theme-text mb-4">Top Categories</h3>
          <div class="space-y-3">
            <div v-for="category in categoryBreakdown.slice(0, 5)" :key="category.name"
              class="flex items-center justify-between p-3 rounded-lg bg-gray-800 bg-opacity-50">
              <span class="text-sm theme-text">{{ category.name }}</span>
              <span class="text-sm font-medium text-red-400">{{ (category.amount * exchangeRate).toLocaleString() }} MMK</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, nextTick, watch } from 'vue';
import {
  Chart as ChartJS,
  ArcElement,
  Tooltip,
  Legend,
  PieController
} from 'chart.js';

ChartJS.register(ArcElement, Tooltip, Legend, PieController);

export default {
  name: 'ChartModal',
  props: {
    show: {
      type: Boolean,
      default: false
    },
    totalIncome: {
      type: Number,
      default: 0
    },
    totalExpenses: {
      type: Number,
      default: 0
    },
    exchangeRate: {
      type: Number,
      default: 4500
    },
    transactions: {
      type: Array,
      default: () => []
    },
    categoryBreakdown: {
      type: Array,
      default: () => []
    },
    incomeExpenseBreakdown: {
      type: Array,
      default: () => []
    }
  },
  setup(props) {
    const pieChartCanvas = ref(null);
    const pieChartInstance = ref(null);

    const getChartColors = (breakdown) => {
      return breakdown.map((item, index) => {
        if (item.type === 'income') {
          const greenColors = ['#22c55e', '#16a34a', '#15803d', '#166534', '#14532d', '#10b981', '#059669', '#047857'];
          return greenColors[index % greenColors.length];
        } else {
          const redColors = ['#ef4444', '#dc2626', '#b91c1c', '#991b1b', '#f97316', '#ea580c', '#eab308', '#ca8a04'];
          return redColors[index % redColors.length];
        }
      });
    };

    const createPieChart = () => {
      if (!pieChartCanvas.value || !props.incomeExpenseBreakdown.length) return;

      // Destroy existing chart
      if (pieChartInstance.value) {
        pieChartInstance.value.destroy();
      }

      const ctx = pieChartCanvas.value.getContext('2d');
      const data = props.incomeExpenseBreakdown.map(item => item.amount);
      const labels = props.incomeExpenseBreakdown.map(item => item.name);
      const colors = getChartColors(props.incomeExpenseBreakdown);

      // Simple pie chart with minimal configuration
      pieChartInstance.value = new ChartJS(ctx, {
        type: 'pie',
        data: { labels, datasets: [{ data, backgroundColor: colors, borderWidth: 2 }] },
        options: { responsive: true, maintainAspectRatio: false, plugins: { legend: { display: false } } }
      });
    };

    // Watch for show prop changes and create chart when modal opens
    watch(() => props.show, (newValue) => {
      if (newValue) {
        nextTick(() => {
          createPieChart();
        });
      } else {
        // Destroy chart when modal closes
        if (pieChartInstance.value) {
          pieChartInstance.value.destroy();
          pieChartInstance.value = null;
        }
      }
    });

    return {
      pieChartCanvas,
      pieChartInstance,
      getChartColors,
      createPieChart
    };
  },
  emits: ['close']
};
</script>

<style scoped>
.glass-card {
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 24px 0 rgba(31, 38, 135, 0.2);
}

.glass-item {
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.08);
}

.theme-text {
  color: #ffffff;
}

.theme-text-muted {
  color: rgba(255, 255, 255, 0.7);
}

/* Chart bar animation */
.chart-bar {
  transition: width 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}
</style>
