<template>
  <div :class="isDarkMode ? 'theme-dark' : 'theme-light'" class="min-h-screen bg-gradient-to-br from-purple-400 via-blue-500 to-green-400 dark:from-gray-900 dark:via-purple-900 dark:to-gray-800 transition-all duration-500">
    <!-- Animated Background Elements -->
    <div class="fixed inset-0 overflow-hidden pointer-events-none">
      <div class="floating-shape shape-1"></div>
      <div class="floating-shape shape-2"></div>
      <div class="floating-shape shape-3"></div>
      <div class="floating-shape shape-4"></div>
    </div>

    <div class="container mx-auto px-4 sm:py-8 py-4 sm:px-6 relative z-10">
      <!-- Header with Theme Toggle -->
      <div class="flex justify-between items-center mb-8">

      <h1 class="text-2xl sm:text-4xl font-bold theme-text mb-4 pt-4 text-center">Expense List</h1>
        
        <button @click="toggleTheme"
          class="glass-button p-3 rounded-xl hover:scale-110 transition-all duration-300 group">
          <svg v-if="isDarkMode" xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 text-yellow-500 group-hover:rotate-180 transition-transform duration-500" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z" />
          </svg>
          <svg v-else xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 text-purple-600 group-hover:rotate-180 transition-transform duration-500" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M20.354 15.354A9 9 0 018.646 3.646 9.003 9.003 0 0012 21a9.003 9.003 0 008.354-5.646z" />
          </svg>
        </button>
      </div>      
      
      <!-- Desktop Navigation -->
      <div class="hidden md:flex justify-center mb-8">
        <div class="glass-nav flex space-x-2 p-2 rounded-2xl">
          <button @click="openSummaryModal" 
            class="nav-button active bg-gradient-to-r from-green-500 to-emerald-500 text-white px-6 py-3 rounded-xl transition-all duration-300 hover:scale-105 group">
            <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 inline mr-2 group-hover:rotate-12 transition-transform" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 8c-1.657 0-3 .895-3 2s1.343 2 3 2 3 .895 3 2-1.343 2-3 2m0-8c1.11 0 2.08.402 2.599 1M12 8V7m0 1v8m0 0v1m0-1c-1.11 0-2.08-.402-2.599-1M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
            </svg>
            Summary
          </button>
          <button @click="openChartModal" 
            class="nav-button bg-gradient-to-r from-blue-500 to-cyan-500 text-white px-6 py-3 rounded-xl transition-all duration-300 hover:scale-105 group">
            <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 inline mr-2 group-hover:rotate-12 transition-transform" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
            </svg>
            Analytics
          </button>          <button @click="openHistoryModal" 
            class="nav-button bg-gradient-to-r from-purple-500 to-pink-500 text-white px-6 py-3 rounded-xl transition-all duration-300 hover:scale-105 group">
            <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 inline mr-2 group-hover:rotate-12 transition-transform" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
            </svg>
            History
          </button>
          <button @click="openFeedbackModal" 
            class="nav-button bg-gradient-to-r from-orange-500 to-red-500 text-white px-6 py-3 rounded-xl transition-all duration-300 hover:scale-105 group">
            <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 inline mr-2 group-hover:rotate-12 transition-transform" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M7 8h10m0 0V6a2 2 0 00-2-2H9a2 2 0 00-2 2v2m0 0v8a2 2 0 002 2h10a2 2 0 002-2V8M9 12h6m-6 4h6" />
            </svg>
            Feedback
          </button>
        </div>
      </div>      
      <!-- Floating Menu Component -->      <FloatingMenu 
        :is-open="isFloatingMenuOpen"
        @toggle="toggleFloatingMenu"
        @open-summary="openSummaryModal"
        @open-chart="openChartModal"
        @open-history="openHistoryModal"
        @open-feedback="openFeedbackModal"
      />
      <FinancialSummaryModal 
        :show="showSummaryModal"
        :total-income="totalIncome"
        :total-expenses="totalExpenses"
        :net-balance="netBalance"
        :exchange-rate="exchangeRate"
        :transactions="transactions"
        @close="closeSummaryModal"
      />      
        <!-- History Modal Component -->
      <HistoryModal 
        :show="showHistoryModal"
        :deleted-items="deletedItems"
        :exchange-rate="exchangeRate"
        @close="closeHistoryModal"
        @restore-item="restoreItem"
        @permanently-delete="permanentlyDelete"
        @format-date="formatDate"
      />      
      
      <!-- Feedback Modal Component -->
      <FeedbackModal 
        :show="showFeedbackModal"
        @close="closeFeedbackModal"
      />
      
      <!-- Chart Modal Component -->
      <ChartModal 
        :show="showChartModal"
        :total-income="totalIncome"
        :total-expenses="totalExpenses"
        :exchange-rate="exchangeRate"
        :transactions="transactions"
        @close="closeChartModal"
      />

      <!-- Centered Two Column Layout -->
      <div class="flex justify-center items-center min-h-[80vh] p-0 sm:p-4">
        <div class="grid grid-cols-1 xl:grid-cols-2 gap-8 max-w-7xl w-full">          
          <!-- Left Column: Controls and Summary -->
          <div class="space-y-6">            <!-- Add/Edit Transaction Form Component -->
            <AddItem 
              :editing-transaction="editingTransaction"
              :exchange-rate="exchangeRate"
              @add-transaction="addOrUpdateTransaction"
              @update-transaction="addOrUpdateTransaction"
              @cancel-edit="cancelEdit"
              @toggle-advanced="toggleAdvancedFields"
              @toggle-recording="toggleRecording"
              @apply-transcription="applyTranscription"
              @convert-to-dollars="convertToDollars"            />
          </div>          
          <!-- Right Column: Transactions List Component -->
          <div class="space-y-6">            <TransactionsList 
              :transactions="paginatedTransactions"
              :current-page="currentPage"
              :total-pages="totalPages"
              :page-numbers="pageNumbers"
              :export-range="exportRange"
              :exchange-rate="exchangeRate"
              :page-size="pageSize"
              :total-items="transactions.length"
              @edit-transaction="editTransaction"
              @delete-transaction="deleteTransaction"              @export-pdf="exportToPDF"
              @go-to-page="goToPage"
              @update:export-range="exportRange = $event"
              @format-date="formatDate"
              @format-time="formatTime"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, reactive, computed } from 'vue';
import { gsap } from 'gsap';
import Swal from 'sweetalert2';

// Import modular components
import AddItem from './AddItem.vue';
import TransactionsList from './TransactionsList.vue';
import FloatingMenu from './FloatingMenu.vue';
import FinancialSummaryModal from './FinancialSummaryModal.vue';
import ChartModal from './ChartModal.vue';
import HistoryModal from './HistoryModal.vue';
import FeedbackModal from './FeedbackModal.vue';

export default {
  name: 'ExpenseTracker',  components: {
    AddItem,
    TransactionsList,
    FloatingMenu,
    FinancialSummaryModal,
    ChartModal,
    HistoryModal,
    FeedbackModal
  },
  setup() {
    // Core state
    const transactions = ref([]);
    const editingTransaction = ref(null);
    const isDarkMode = ref(true);
    const deletedItems = ref([]);

    // LocalStorage keys
    const LOCAL_KEY = 'transactions';
    const DELETED_KEY = 'deleted_transactions';

    // Helper: Get transactions from localStorage
    function getTransactionsFromLocal() {
      try {
        return JSON.parse(localStorage.getItem(LOCAL_KEY)) || [];
      } catch {
        return [];
      }
    }
    // Helper: Save transactions to localStorage
    function setTransactionsToLocal(arr) {
      localStorage.setItem(LOCAL_KEY, JSON.stringify(arr));
    }
    // Helper: Get deleted transactions from localStorage
    function getDeletedFromLocal() {
      try {
        return JSON.parse(localStorage.getItem(DELETED_KEY)) || [];
      } catch {
        return [];
      }
    }
    // Helper: Save deleted transactions to localStorage
    function setDeletedToLocal(arr) {
      localStorage.setItem(DELETED_KEY, JSON.stringify(arr));
    }

    // Pagination state
    const currentPage = ref(1);
    const pageSize = ref(10);
    const exportRange = ref('weekly');
    // Modal state
    const isFloatingMenuOpen = ref(false);
    const showSummaryModal = ref(false);
    const showChartModal = ref(false);
    const showHistoryModal = ref(false);
    const showFeedbackModal = ref(false);

    // Form state for child components
    const form = reactive({
      id: null,
      name: '',
      type: 'outcome',
      price_usd: null,
      price_mmk: null,
      description: '',
      category: '',
      date: new Date().toISOString().slice(0, 10),
      time: new Date().toTimeString().slice(0, 5),
    });
    // State for child components
    const showAdvancedFields = ref(false);

    // --- Computed properties for summary and pagination ---
    const totalIncome = computed(() => {
      return transactions.value
        .filter(tx => tx.type === 'income')
        .reduce((sum, tx) => sum + (parseFloat(tx.price_usd) || 0), 0);
    });
    const totalExpenses = computed(() => {
      return transactions.value
        .filter(tx => tx.type === 'outcome')
        .reduce((sum, tx) => sum + (parseFloat(tx.price_usd) || 0), 0);
    });
    const netBalance = computed(() => totalIncome.value - totalExpenses.value);
    const totalPages = computed(() => Math.ceil(transactions.value.length / pageSize.value) || 1);
    const paginatedTransactions = computed(() => {
      const start = (currentPage.value - 1) * pageSize.value;
      return transactions.value.slice(start, start + pageSize.value);
    });
    const pageNumbers = computed(() => {
      return Array.from({ length: totalPages.value }, (_, i) => i + 1);
    });

    // CRUD: Fetch all transactions
    function fetchTransactions() {
      transactions.value = getTransactionsFromLocal();
    }
    // CRUD: Fetch deleted transactions
    function fetchDeletedTransactions() {
      deletedItems.value = getDeletedFromLocal();
    }
    // CRUD: Add or update transaction
    function addOrUpdateTransaction(transactionData) {
      let txs = getTransactionsFromLocal();
      if (editingTransaction.value) {
        // Update
        const idx = txs.findIndex(t => t.id === editingTransaction.value.id);
        if (idx !== -1) {
          txs[idx] = { ...editingTransaction.value, ...transactionData };
          setTransactionsToLocal(txs);
          fetchTransactions();
          resetForm();
          animateFormSubmission();
        }
      } else {
        // Add
        const newTx = { ...transactionData, id: Date.now() };
        txs.unshift(newTx);
        setTransactionsToLocal(txs);
        fetchTransactions();
        resetForm();
        animateFormSubmission();
      }
    }
    // CRUD: Edit transaction
    function editTransaction(tx) {
      editingTransaction.value = { ...tx };
      Object.assign(form, tx);
    }    // CRUD: Delete transaction
    function deleteTransaction(txOrId) {
      let txs = getTransactionsFromLocal();
      let dels = getDeletedFromLocal();
      
      // Handle both transaction object or just ID
      let transactionToDelete;
      if (typeof txOrId === 'object' && txOrId.id) {
        // If it's a transaction object
        transactionToDelete = txOrId;
      } else {
        // If it's just an ID, find the transaction
        transactionToDelete = txs.find(t => t.id === txOrId);
        if (!transactionToDelete) {
          console.error('Transaction not found with ID:', txOrId);
          return;
        }
      }
        // Remove from transactions and add to deleted items
      txs = txs.filter(t => t.id !== transactionToDelete.id);
      dels.unshift({ ...transactionToDelete, deleted_at: new Date().toISOString() });
      setTransactionsToLocal(txs);
      setDeletedToLocal(dels);
      fetchTransactions();
      fetchDeletedTransactions();
    }    // Restore deleted transaction
    function restoreItem(tx) {
      let txs = getTransactionsFromLocal();
      let dels = getDeletedFromLocal();
      dels = dels.filter(t => t.id !== tx.id);
      
      // Remove the deleted_at field before restoring
      const restoredTx = { ...tx };
      delete restoredTx.deleted_at;
      
      txs.unshift(restoredTx);
      setTransactionsToLocal(txs);
      setDeletedToLocal(dels);
      fetchTransactions();
      fetchDeletedTransactions();
    }
    // Permanently delete
    function permanentlyDelete(tx) {
      let dels = getDeletedFromLocal();
      dels = dels.filter(t => t.id !== tx.id);
      setDeletedToLocal(dels);
      fetchDeletedTransactions();
    }
    // Reset form
    function resetForm() {
      editingTransaction.value = null;
      Object.assign(form, {
        id: null,
        name: '',
        type: 'outcome',
        price_usd: null,
        price_mmk: null,
        description: '',
        category: '',
        date: new Date().toISOString().slice(0, 10),
        time: new Date().toTimeString().slice(0, 5),
      });
    }
    function cancelEdit() {
      resetForm();
    }
    // Dummy for UI
    function animateFormSubmission() {}

    // Theme toggle
    function toggleTheme() {
      isDarkMode.value = !isDarkMode.value;
      localStorage.setItem('expense-tracker-theme', isDarkMode.value ? 'dark' : 'light');
    }

    // Export to PDF (offline, using jsPDF)
    async function exportToPDF() {
      if (!transactions.value.length) {
        Swal.fire({
          icon: 'info',
          title: 'No Data',
          text: 'No transactions to export!',
          confirmButtonColor: '#3b82f6'
        });
        return;
      }
      // Dynamically import jsPDF and autoTable
      const [{ jsPDF }, autoTable] = await Promise.all([
        import('jspdf'),
        import('jspdf-autotable')
      ]);
      // Filter transactions by exportRange
      const today = new Date();
      let startDate, endDate;
      if (exportRange.value === 'daily') {
        startDate = new Date(today);
        endDate = new Date(today);
      } else if (exportRange.value === 'weekly') {
        const day = today.getDay();
        startDate = new Date(today);
        startDate.setDate(today.getDate() - day + (day === 0 ? -6 : 1)); // Monday
        endDate = new Date(startDate);
        endDate.setDate(startDate.getDate() + 6); // Sunday
      } else if (exportRange.value === 'monthly') {
        startDate = new Date(today.getFullYear(), today.getMonth(), 1);
        endDate = new Date(today.getFullYear(), today.getMonth() + 1, 0);
      } else if (exportRange.value === 'yearly') {
        startDate = new Date(today.getFullYear(), 0, 1);
        endDate = new Date(today.getFullYear(), 11, 31);
      }
      // Format date as yyyy-mm-dd
      const format = d => d.toISOString().slice(0, 10);
      const filtered = transactions.value.filter(tx => {
        const d = new Date(tx.date);
        return d >= startDate && d <= endDate;
      });
      if (!filtered.length) {
        Swal.fire({
          icon: 'info',
          title: 'No Data',
          text: 'No transactions in selected range!',
          confirmButtonColor: '#3b82f6'
        });
        return;
      }
      // Generate PDF
      const doc = new jsPDF();
      doc.setFontSize(16);
      doc.text('Expense Tracker Report', 14, 16);
      doc.setFontSize(12);
      doc.text(`Range: ${exportRange.value.charAt(0).toUpperCase() + exportRange.value.slice(1)} (${format(startDate)} to ${format(endDate)})`, 14, 24);
      // Prepare table data
      const tableData = filtered.map((tx, i) => [
        i + 1,
        tx.name,
        tx.type,
        tx.price_usd ? `$${tx.price_usd}` : '',
        tx.price_mmk ? `${tx.price_mmk} Ks` : '',
        tx.category || '',
        tx.date,
        tx.time
      ]);
      autoTable.default(doc, {
        head: [[
          '#', 'Name', 'Type', 'USD', 'MMK', 'Category', 'Date', 'Time'
        ]],
        body: tableData,
        startY: 30,
        styles: { fontSize: 10 }
      });
      // Save PDF
      const dateStr = today.toISOString().slice(0, 10);
      let filename = `expense-tracker-${exportRange.value}-${dateStr}.pdf`;
      if (exportRange.value !== 'daily') {
        const startStr = format(startDate);
        const endStr = format(endDate);
        filename = `expense-tracker-${exportRange.value}-${startStr}-to-${endStr}.pdf`;
      }
      doc.save(filename);
      Swal.fire({
        icon: 'success',
        title: 'Export Successful!',
        text: `PDF exported successfully for ${exportRange.value} period.`,
        confirmButtonColor: '#3b82f6',
        timer: 3000,
        timerProgressBar: true
      });
    }

    // --- Add missing UI state mutators and modal toggles ---
    function goToPage(page) {
      if (page >= 1 && page <= totalPages.value) {
        currentPage.value = page;
      }
    }
    function toggleFloatingMenu() {
      isFloatingMenuOpen.value = !isFloatingMenuOpen.value;
    }
    function openSummaryModal() {
      showSummaryModal.value = true;
    }
    function closeSummaryModal() {
      showSummaryModal.value = false;
    }
    function openChartModal() {
      showChartModal.value = true;
    }
    function closeChartModal() {
      showChartModal.value = false;
    }
    function openFeedbackModal() {
      showFeedbackModal.value = true;
    }
    function closeFeedbackModal() {
      showFeedbackModal.value = false;
    }    function toggleAdvancedFields() {
      showAdvancedFields.value = !showAdvancedFields.value;
    }

    // History modal functions
    function openHistoryModal() {
      showHistoryModal.value = true;
    }
    function closeHistoryModal() {
      showHistoryModal.value = false;
    }

    // Exchange rate (static for now, could be made dynamic)
    const exchangeRate = ref(1800); // USD to MMK rate

    // Date and time formatting functions
    function formatDate(dateString) {
      const date = new Date(dateString);
      return date.toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'short',
        day: 'numeric'
      });
    }

    function formatTime(timeString) {
      return timeString || '00:00';
    }

    // Voice recording functions (placeholder implementations)
    function toggleRecording() {
      // Placeholder for voice recording functionality
      console.log('Toggle recording function called');
    }

    function applyTranscription() {
      // Placeholder for applying voice transcription
      console.log('Apply transcription function called');
    }

    function convertToDollars() {
      // Placeholder for currency conversion
      console.log('Convert to dollars function called');
    }

    // On app load, fetch from localStorage
    onMounted(() => {
      const savedTheme = localStorage.getItem('expense-tracker-theme');
      if (savedTheme) {
        isDarkMode.value = savedTheme === 'dark';
      }
      fetchTransactions();
      fetchDeletedTransactions();
      gsap.from('.glassmorphism', { opacity: 0, scale: 0.9, duration: 0.8, ease: 'power2.out' });
      gsap.from('h1', { opacity: 0, y: -30, duration: 0.7, delay: 0.3 });
    });
    // --- All function and variable declarations are above this line ---
    return {
      transactions,
      form,
      editingTransaction,
      addOrUpdateTransaction,
      editTransaction,
      deleteTransaction,
      resetForm,
      cancelEdit,
      restoreItem,
      permanentlyDelete,
      isDarkMode,
      toggleTheme,
      exportRange,
      exportToPDF,
      totalIncome,
      totalExpenses,
      netBalance,
      fetchTransactions,
      fetchDeletedTransactions,
      paginatedTransactions,
      currentPage,
      totalPages,
      goToPage,
      pageSize,
      pageNumbers,
      // Floating Menu
      isFloatingMenuOpen,
      showSummaryModal,
      showChartModal,
      showHistoryModal,
      showFeedbackModal,
      deletedItems,
      toggleFloatingMenu,
      openSummaryModal,
      closeSummaryModal,
      openChartModal,
      closeChartModal,      openFeedbackModal,
      closeFeedbackModal,
      openHistoryModal,
      closeHistoryModal,
      // Exchange rate and formatting
      exchangeRate,
      formatDate,
      formatTime,
      // Voice recording functions
      toggleRecording,
      applyTranscription,
      convertToDollars,
      // Advanced fields expansion
      showAdvancedFields,
      toggleAdvancedFields,
    };
  },
};
</script>

<style scoped>
.theme-dark {
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f3460 100%);
}

.theme-light {
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 50%, #cbd5e1 100%);
}

.glassmorphism {
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
}

.theme-dark .glassmorphism {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.theme-light .glassmorphism {
  background: rgba(255, 255, 255, 0.25);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.theme-dark .theme-text {
  color: #ffffff;
}

.theme-light .theme-text {
  color: #1f2937;
}

.theme-dark .theme-text-muted {
  color: rgba(255, 255, 255, 0.7);
}

.theme-light .theme-text-muted {
  color: rgba(31, 41, 55, 0.7);
}

.theme-toggle-btn {
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 2px 12px 0 rgba(31, 38, 135, 0.2);
}

.theme-dark .theme-toggle-btn {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
}

.theme-light .theme-toggle-btn {
  background: rgba(255, 255, 255, 0.3);
  color: #1f2937;
}

.theme-toggle-btn:hover {
  transform: scale(1.05);
  box-shadow: 0 4px 20px 0 rgba(31, 38, 135, 0.3);
}

.glassmorphism {
  animation: fadeInUp 0.8s ease-out;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Advanced Fields Animation */
.fade-slide-enter-active,
.fade-slide-leave-active {
  transition: all 0.3s ease;
}

.fade-slide-enter-from {
  opacity: 0;
  transform: translateY(-10px);
}

.fade-slide-leave-to {
  opacity: 0;
  transform: translateY(-10px);
}

/* See More Button Styles */
.rotate-180 {
  transform: rotate(180deg);
}

/* Voice Recording Styles */
.voice-btn {
  min-width: 140px;
  font-weight: 500;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.voice-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);
}

.voice-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.recording-pulse {
  animation: pulse 1.5s ease-in-out infinite;
}

