<template>
  <div class="space-y-6">
    <div class="glass-card p-6 rounded-lg shadow-lg">
      <div class="flex justify-between items-center mb-6">
        <h2 class="text-md sm:text-2xl font-semibold theme-text">Expenses</h2>
        <div class="flex items-center space-x-2">
          <ExportPDF 
            :transactions="transactions" 
            :exportRange="exportRange"
            @update:exportRange="updateExportRange"
          />
        </div>
      </div>      <ul v-if="transactions.length > 0" class="space-y-4 mb-8">
        <li v-for="transaction in transactions" :key="transaction.id"
          class="transaction-item glass-item p-5 rounded-lg shadow-md flex flex-col md:flex-row justify-between items-start md:items-center transition-all duration-300 hover:shadow-xl"
          :class="transaction.type === 'income' ? 'border-l-4 border-green-400' : 'border-l-4 border-red-400'">
          <div class="flex-grow mb-4 md:mb-0">
            <h3 class="text-xl font-semibold theme-text">{{ transaction.name }}</h3>
            <p class="text-sm theme-text-muted">{{ transaction.category }}</p>
            <p class="text-xs theme-text-muted hidden sm:inline-flex">{{ transaction.description }}</p>
          </div>
          <div class="md:text-left sm:mb-4 md:mb-0 mb-8 md:mx-4">
            <p class="text-lg font-medium"
              :class="transaction.type === 'income' ? 'text-green-300' : 'text-red-300'">
              {{ transaction.type === 'income' ? '+' : '-' }} {{ (transaction.price_mmk || 0).toLocaleString() }} MMK
            </p>
            <p class="text-sm theme-text-muted">${{ (transaction.price_usd || 0).toFixed(2) }}</p>
            <p class="text-xs theme-text-muted">{{ formatDate(transaction.date) }} at {{ formatTime(transaction.time) }}</p>
          </div>
          <div class="flex space-x-3 self-end">
            <button @click="$emit('edit-transaction', transaction)" class="btn-icon text-blue-300 hover:text-blue-100">
              <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z" />
              </svg>
            </button>
            <button @click="$emit('delete-transaction', transaction.id)" class="btn-icon text-red-400 hover:text-red-200">
              <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
              </svg>
            </button>
          </div>
        </li>
      </ul>

      <!-- No transactions message -->
      <div v-else class="text-center py-12">
        <svg xmlns="http://www.w3.org/2000/svg" class="h-16 w-16 mx-auto theme-text-muted mb-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 5H7a2 2 0 00-2 2v10a2 2 0 002 2h8a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2" />
        </svg>
        <h3 class="text-lg font-medium theme-text mb-2">No transactions yet</h3>
        <p class="theme-text-muted">Add your first transaction to get started.</p>
      </div>      <!-- Pagination Component -->      <Pagination 
        :current-page="currentPage"
        :total-pages="totalPages"
        :total-items="totalItems"
        :page-size="pageSize"
        :show-info="true"
        @go-to-page="$emit('go-to-page', $event)"
      />
    </div>
  </div>
</template>

<script>
import ExportPDF from './ExportPDF.vue'
import Pagination from './Pagination.vue'

export default {
  name: 'TransactionsList',
  components: {
    ExportPDF,
    Pagination
  },  props: {
    transactions: {
      type: Array,
      required: true
    },
    currentPage: {
      type: Number,
      default: 1
    },
    totalPages: {
      type: Number,
      required: true
    },
    pageNumbers: {
      type: Array,
      default: () => []
    },
    exportRange: {
      type: String,
      default: 'weekly'
    },    exchangeRate: {
      type: Number,
      default: 4500
    },
    pageSize: {
      type: Number,
      default: 10
    },
    totalItems: {
      type: Number,
      default: 0
    }
  },
  emits: ['edit-transaction', 'delete-transaction', 'export-pdf', 'go-to-page', 'update:export-range', 'format-date', 'format-time'],  setup(props, { emit }) {
    const formatDate = (dateString) => {
      const options = { year: 'numeric', month: 'long', day: 'numeric' }
      return new Date(dateString).toLocaleDateString(undefined, options)
    }

    const formatTime = (timeString) => {
      if (!timeString) return ''
      const [hours, minutes] = timeString.split(':')
      const date = new Date()
      date.setHours(parseInt(hours), parseInt(minutes))
      return date.toLocaleTimeString(undefined, { hour: '2-digit', minute: '2-digit', hour12: true })
    }

    const updateExportRange = (newRange) => {
      emit('update:export-range', newRange)
    }

    const handleEditTransaction = (transaction) => {
      emit('edit-transaction', transaction)
    }

    const handleDeleteTransaction = (id) => {
      emit('delete-transaction', id)
    }

    const handleExportPdf = () => {
      emit('export-pdf')
    }

    return {
      formatDate,
      formatTime,
      updateExportRange,
      handleEditTransaction,
      handleDeleteTransaction,
      handleExportPdf
    }
  }
}
</script>

<style scoped>
.pagination-btn {
  padding: 0.75rem 1rem;
  border-radius: 0.375rem;
  transition: all 0.2s ease-in-out;
  background-color: #e5e7eb;
  color: #374151;
}

.dark .pagination-btn {
  background-color: #374151;
  color: #d1d5db;
}

.pagination-btn:hover:not(:disabled) {
  background-color: #d1d5db;
}

.dark .pagination-btn:hover:not(:disabled) {
  background-color: #4b5563;
}

.pagination-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.transaction-item {
  transition: all 0.3s ease;
}

.transaction-item:hover {
  transform: translateY(-2px);
}

.btn-icon {
  padding: 0.5rem;
  border-radius: 9999px;
  transition: all 0.2s ease-in-out;
}

.btn-icon:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.theme-dark .theme-text {
  color: #ffffff;
}

.theme-light {
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 50%, #cbd5e1 100%);
}

.theme-light .theme-text {
  color: #1f2937;
}

.theme-dark .theme-text-muted {
  color: rgba(255, 255, 255, 0.7);
}

.theme-light .theme-text-muted {
  color: rgba(31, 41, 55, 0.7);
}

.glass-item {
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.08);
}

.theme-dark .glass-item {
  background: rgba(255, 255, 255, 0.05);
}

.theme-light .glass-item {
  background: rgba(255, 255, 255, 0.3);
}

.glass-card {
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 24px 0 rgba(31, 38, 135, 0.2);
}

.theme-dark .glass-card {
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.theme-light .glass-card {
  background: rgba(255, 255, 255, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.3);
}
</style>
