<template>  <div v-if="totalPages > 1" class="pagination-container">
    <!-- Pagination Controls -->
    <div class="pagination-controls">
      <!-- First Page Button -->
      <button 
        @click="$emit('go-to-page', 1)" 
        :disabled="currentPage === 1" 
        class="pagination-button"
        title="First Page"
      >
        <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M11 19l-7-7 7-7m8 14l-7-7 7-7" />
        </svg>
      </button>

      <!-- Previous Page Button -->
      <button 
        @click="$emit('go-to-page', currentPage - 1)" 
        :disabled="currentPage === 1" 
        class="pagination-button"
        title="Previous Page"
      >
        <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 19l-7-7 7-7" />
        </svg>
      </button>

      <!-- Page Numbers -->
      <div class="page-numbers">
        <!-- Show first page if not in range -->
        <button 
          v-if="showFirstPage" 
          @click="$emit('go-to-page', 1)" 
          :class="['pagination-button', { 'active': 1 === currentPage }]"
        >
          1
        </button>
        
        <!-- Show ellipsis if there's a gap -->
        <span v-if="showFirstEllipsis" class="pagination-ellipsis">...</span>
        
        <!-- Page range -->
        <button 
          v-for="page in visiblePages" 
          :key="page" 
          @click="$emit('go-to-page', page)" 
          :class="['pagination-button', { 'active': page === currentPage }]"
        >
          {{ page }}
        </button>
        
        <!-- Show ellipsis if there's a gap -->
        <span v-if="showLastEllipsis" class="pagination-ellipsis">...</span>
        
        <!-- Show last page if not in range -->
        <button 
          v-if="showLastPage" 
          @click="$emit('go-to-page', totalPages)" 
          :class="['pagination-button', { 'active': totalPages === currentPage }]"
        >
          {{ totalPages }}
        </button>
      </div>

      <!-- Next Page Button -->
      <button 
        @click="$emit('go-to-page', currentPage + 1)" 
        :disabled="currentPage === totalPages" 
        class="pagination-button"
        title="Next Page"
      >
        <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 5l7 7-7 7" />
        </svg>
      </button>

      <!-- Last Page Button -->
      <button 
        @click="$emit('go-to-page', totalPages)" 
        :disabled="currentPage === totalPages" 
        class="pagination-button"
        title="Last Page"
      >
        <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M13 5l7 7-7 7M5 5l7 7-7 7" />
        </svg>
      </button>
    </div>

    <!-- Page Info -->
    <div v-if="showInfo" class="pagination-info">
      <span class="text-sm text-gray-600 dark:text-gray-300">
        Page {{ currentPage }} of {{ totalPages }}
        <span v-if="totalItems" class="ml-2">
          ({{ startItem }}-{{ endItem }} of {{ totalItems }} {{ totalItems === 1 ? 'item' : 'items' }})
        </span>
      </span>
    </div>
  </div>
</template>

<script>
import { computed, ref, onMounted, onUnmounted } from 'vue'

export default {
  name: 'Pagination',
  props: {
    currentPage: {
      type: Number,
      required: true,
      validator: value => value > 0
    },
    totalPages: {
      type: Number,
      required: true,
      validator: value => value >= 0
    },
    totalItems: {
      type: Number,
      default: 0
    },
    maxVisiblePages: {
      type: Number,
      default: 5,
      validator: value => value > 0
    },
    pageSize: {
      type: Number,
      default: 10
    },    showInfo: {
      type: Boolean,
      default: true
    }
  },
  emits: ['go-to-page'],  setup(props) {
    const isMobile = ref(false)
    
    const checkMobile = () => {
      isMobile.value = window.innerWidth <= 640
    }
    
    onMounted(() => {
      checkMobile()
      window.addEventListener('resize', checkMobile)
    })
    
    onUnmounted(() => {
      window.removeEventListener('resize', checkMobile)
    })
    
    const visiblePages = computed(() => {
      const { currentPage, totalPages, maxVisiblePages } = props
      const pages = []
      
      // For mobile-friendly pagination, limit visible pages more aggressively
      const effectiveMaxVisible = isMobile.value ? 3 : maxVisiblePages
      
      if (totalPages <= effectiveMaxVisible) {
        // Show all pages if total is within limit
        for (let i = 1; i <= totalPages; i++) {
          pages.push(i)
        }
      } else {
        // Calculate range around current page
        const half = Math.floor(effectiveMaxVisible / 2)
        let start = Math.max(1, currentPage - half)
        let end = Math.min(totalPages, start + effectiveMaxVisible - 1)
        
        // Adjust start if end is at totalPages
        if (end === totalPages) {
          start = Math.max(1, end - effectiveMaxVisible + 1)
        }
        
        // On mobile, don't exclude first and last pages from visible range
        if (!isMobile.value) {
          // Exclude first and last page from visible range if they'll be shown separately
          if (start === 1) {
            start = 2
          }
          if (end === totalPages) {
            end = totalPages - 1
          }
        }
        
        for (let i = start; i <= end; i++) {
          pages.push(i)
        }
      }
        return pages
    })

    const showFirstPage = computed(() => {
      if (isMobile.value) return false // Don't show separate first page on mobile
      
      return props.totalPages > props.maxVisiblePages && 
             !visiblePages.value.includes(1) && 
             props.currentPage > Math.floor(props.maxVisiblePages / 2) + 1
    })

    const showLastPage = computed(() => {
      if (isMobile.value) return false // Don't show separate last page on mobile
      
      return props.totalPages > props.maxVisiblePages && 
             !visiblePages.value.includes(props.totalPages) && 
             props.currentPage < props.totalPages - Math.floor(props.maxVisiblePages / 2)
    })

    const showFirstEllipsis = computed(() => {
      if (isMobile.value) return false // Don't show ellipsis on mobile
      
      return showFirstPage.value && visiblePages.value[0] > 2
    })

    const showLastEllipsis = computed(() => {
      if (isMobile.value) return false // Don't show ellipsis on mobile
      
      return showLastPage.value && visiblePages.value[visiblePages.value.length - 1] < props.totalPages - 1
    })

    const startItem = computed(() => {
      return (props.currentPage - 1) * props.pageSize + 1
    })

    const endItem = computed(() => {
      return Math.min(props.currentPage * props.pageSize, props.totalItems)
    })

    return {
      visiblePages,
      showFirstPage,
      showLastPage,
      showFirstEllipsis,
      showLastEllipsis,
      startItem,
      endItem
    }
  }
}
</script>

<style scoped>
.pagination-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  margin-top: 1.5rem;
  flex-wrap: wrap;
}

.pagination-controls {
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.page-numbers {
  display: flex;
  gap: 0.25rem;
}

.pagination-button {
  padding: 0.5rem 0.75rem;
  font-size: 0.875rem;
  font-weight: 500;
  border-radius: 0.5rem;
  border: 1px solid #d1d5db;
  transition: all 0.2s;
  color: #374151;
  background-color: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
  min-width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.pagination-button:hover:not(:disabled) {
  background-color: rgba(249, 250, 251, 0.9);
  transform: scale(1.05);
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}

.pagination-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.pagination-button:focus {
  outline: none;
  box-shadow: 0 0 0 2px #3b82f6;
}

.pagination-button.active {
  background-color: #3b82f6;
  color: white;
  border-color: #3b82f6;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}

.pagination-button.active:hover {
  background-color: #2563eb;
}

.pagination-ellipsis {
  padding: 0.5rem 0.75rem;
  font-size: 0.875rem;
  color: #6b7280;
  display: flex;
  align-items: center;
}

.pagination-info {
  margin-left: 1rem;
  color: #6b7280;
  font-size: 0.875rem;
}

/* Dark mode styles */
@media (prefers-color-scheme: dark) {
  .pagination-button {
    border-color: #4b5563;
    color: #d1d5db;
    background-color: rgba(31, 41, 55, 0.8);
  }
  
  .pagination-button:hover:not(:disabled) {
    background-color: rgba(55, 65, 81, 0.9);
  }
  
  .pagination-button:disabled:hover {
    background-color: rgba(31, 41, 55, 0.8);
  }
  
  .pagination-button.active {
    background-color: #2563eb;
    border-color: #2563eb;
  }
  
  .pagination-button.active:hover {
    background-color: #1d4ed8;
  }
  
  .pagination-ellipsis {
    color: #9ca3af;
  }
    .pagination-info {
    color: #9ca3af;
  }
}

/* Mobile responsive */
@media (max-width: 640px) {
  .pagination-container {
    flex-direction: column;
    gap: 0.5rem;
    margin-top: 1rem;
  }
  
  .pagination-controls {
    justify-content: center;
    flex-wrap: wrap;
    gap: 0.125rem;
  }
  
  .pagination-button {
    padding: 0.375rem 0.5rem;
    font-size: 0.75rem;
    min-width: 32px;
    height: 32px;
  }
  
  /* Hide first/last buttons on mobile to save space */
  .pagination-button[title="First Page"],
  .pagination-button[title="Last Page"] {
    display: none;
  }
  
  /* Simplify page numbers display */
  .page-numbers {
    gap: 0.125rem;
    max-width: 100%;
    overflow: hidden;
  }
  
  /* Hide ellipsis on very small screens */
  .pagination-ellipsis {
    display: none;
  }
  
  /* Show only current page and immediate neighbors */
  .pagination-info {
    display: block;
    width: 100%;
    text-align: center;
    margin-top: 0.25rem;
    margin-left: 0;
    font-size: 0.75rem;
    order: -1; /* Move info to top */
  }
}

/* Extra small screens - even more compact */
@media (max-width: 480px) {
  .pagination-container {
    gap: 0.25rem;
  }
  
  .pagination-button {
    padding: 0.25rem 0.375rem;
    font-size: 0.7rem;
    min-width: 28px;
    height: 28px;
  }
  
  .pagination-controls {
    gap: 0.0625rem;
  }
  
  .page-numbers {
    gap: 0.0625rem;
  }
  
  /* Show only 3 page numbers max on very small screens */
  .page-numbers .pagination-button:nth-child(n+4) {
    display: none;
  }
  
  .pagination-info {
    font-size: 0.7rem;
  }
}
</style>
