            <div class="flex flex-col lg:flex-row lg:items-center lg:justify-between mb-4 lg:mb-6">
              <div class="flex items-center space-x-2 mb-4 lg:mb-0">
                <div class="w-6 lg:w-8 h-6 lg:h-8 bg-gradient-to-r from-purple-500 to-pink-500 rounded-lg flex items-center justify-center">
                  <svg xmlns="http://www.w3.org/2000/svg" class="h-3 lg:h-4 w-3 lg:w-4 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M8 12h.01M12 12h.01M16 12h.01M21 12c0 4.418-4.03 8-9 8a9.863 9.863 0 01-4.255-.949L3 20l1.395-3.72C3.512 15.042 3 13.574 3 12c0-4.418 4.03-8 9-8s9 3.582 9 8z" />
                  </svg>
                </div>
                <h2 class="text-lg lg:text-2xl font-semibold theme-text">User Feedback</h2>
              </div>              <button class="glass-button bg-gradient-to-r from-purple-500 to-pink-600 hover:from-purple-600 hover:to-pink-700 text-white px-3 lg:px-4 py-2 rounded-lg font-medium transition-all duration-300 hover:scale-105 touch-manipulation text-sm lg:text-base" @click="fetchFeedback">
                <svg xmlns="http://www.w3.org/2000/svg" class="h-3 lg:h-4 w-3 lg:w-4 inline mr-2" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                </svg>
                Refresh
              </button>
            </div>
            
            <!-- Filters Section -->
            <div class="glass-item p-4 mb-6 rounded-lg">
              <div class="flex flex-wrap gap-4 items-end">
                <!-- Email Filter -->
                <div class="flex-1 min-w-[200px]">
                  <label class="block text-sm font-medium theme-text mb-2">Filter by Email</label>
                  <input 
                    v-model="feedbackFilters.email" 
                    @input="onFilterChange"
                    type="text" 
                    placeholder="Search email..."
                    class="w-full px-3 py-2 bg-white/10 border border-white/20 rounded-lg text-white placeholder-gray-400 focus:border-purple-500 focus:outline-none"
                  />
                </div>
                
                <!-- Rating Filter -->
                <div class="flex-1 min-w-[150px]">
                  <label class="block text-sm font-medium theme-text mb-2">Filter by Rating</label>
                  <select 
                    v-model="feedbackFilters.rating" 
                    @change="onFilterChange"
                    class="w-full px-3 py-2 bg-white/10 border border-white/20 rounded-lg text-white focus:border-purple-500 focus:outline-none"
                  >
                    <option value="">All Ratings</option>
                    <option value="5">⭐⭐⭐⭐⭐ (5)</option>
                    <option value="4">⭐⭐⭐⭐ (4)</option>
                    <option value="3">⭐⭐⭐ (3)</option>
                    <option value="2">⭐⭐ (2)</option>
                    <option value="1">⭐ (1)</option>
                  </select>
                </div>
                
                <!-- Category Filter -->
                <div class="flex-1 min-w-[200px]">
                  <label class="block text-sm font-medium theme-text mb-2">Filter by Category</label>
                  <select 
                    v-model="feedbackFilters.category" 
                    @change="onFilterChange"
                    class="w-full px-3 py-2 bg-white/10 border border-white/20 rounded-lg text-white focus:border-purple-500 focus:outline-none"
                  >
                    <option value="">All Categories</option>
                    <option v-for="cat in availableCategories" :key="cat.value" :value="cat.value">
                      {{ cat.label }}
                    </option>
                  </select>
                </div>
                
                <!-- Date Range -->
                <div class="flex-1 min-w-[150px]">
                  <label class="block text-sm font-medium theme-text mb-2">From Date</label>
                  <input 
                    v-model="feedbackFilters.dateFrom" 
                    @change="onFilterChange"
                    type="date" 
                    class="w-full px-3 py-2 bg-white/10 border border-white/20 rounded-lg text-white focus:border-purple-500 focus:outline-none"
                  />
                </div>
                
                <div class="flex-1 min-w-[150px]">
                  <label class="block text-sm font-medium theme-text mb-2">To Date</label>
                  <input 
                    v-model="feedbackFilters.dateTo" 
                    @change="onFilterChange"
                    type="date" 
                    class="w-full px-3 py-2 bg-white/10 border border-white/20 rounded-lg text-white focus:border-purple-500 focus:outline-none"
                  />
                </div>
                
                <!-- Clear Filters Button -->
                <div>
                  <button 
                    @click="clearFilters"
                    class="px-4 py-2 bg-gray-600 hover:bg-gray-700 text-white rounded-lg transition-all duration-300"
                  >
                    Clear
                  </button>
                </div>
              </div>
            </div>
              <!-- Results Summary -->
            <div class="flex justify-between items-center mb-4">
              <p class="theme-text-muted text-sm">
                Showing {{ paginatedFeedback.length }} of {{ totalItems }} feedback messages
              </p>
              <div class="flex items-center space-x-2">
                <label class="theme-text text-sm">Per page:</label>
                <select 
                  v-model="feedbackPagination.itemsPerPage" 
                  @change="onFilterChange"
                  class="px-2 py-1 bg-white/10 border border-white/20 rounded text-white text-sm focus:border-purple-500 focus:outline-none"
                >
                  <option value="5">5</option>
                  <option value="10">10</option>
                  <option value="20">20</option>
                  <option value="50">50</option>
                </select>
              </div>
            </div>
            
            <div v-if="filteredFeedback.length === 0" class="text-center py-8 lg:py-12">
              <div class="w-12 lg:w-16 h-12 lg:h-16 bg-gradient-to-r from-gray-500 to-gray-600 rounded-full mx-auto mb-4 flex items-center justify-center">
                <svg xmlns="http://www.w3.org/2000/svg" class="h-6 lg:h-8 w-6 lg:w-8 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M20 13V6a2 2 0 00-2-2H6a2 2 0 00-2 2v7m16 0v5a2 2 0 01-2 2H6a2 2 0 01-2-2v-5m16 0h-2.586a1 1 0 00-.707.293l-2.414 2.414a1 1 0 01-.707.293h-3.172a1 1 0 01-.707-.293l-2.414-2.414A1 1 0 006.586 13H4" />
                </svg>
              </div>
              <p class="theme-text-muted text-lg lg:text-xl mb-2">No feedback messages found</p>
              <p class="theme-text-muted text-sm lg:text-base">User feedback will appear here when submitted</p>
            </div>
              <div v-if="filteredFeedback.length === 0" class="text-center py-8 lg:py-12">
              <div class="w-12 lg:w-16 h-12 lg:h-16 bg-gradient-to-r from-gray-500 to-gray-600 rounded-full mx-auto mb-4 flex items-center justify-center">
                <svg xmlns="http://www.w3.org/2000/svg" class="h-6 lg:h-8 w-6 lg:w-8 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M20 13V6a2 2 0 00-2-2H6a2 2 0 00-2 2v7m16 0v5a2 2 0 01-2 2H6a2 2 0 01-2-2v-5m16 0h-2.586a1 1 0 00-.707.293l-2.414 2.414a1 1 0 01-.707.293h-3.172a1 1 0 01-.707-.293l-2.414-2.414A1 1 0 006.586 13H4" />
                </svg>
              </div>
              <p class="theme-text-muted text-lg lg:text-xl mb-2">No feedback messages found</p>
              <p class="theme-text-muted text-sm lg:text-base">{{ feedbackList.length === 0 ? 'User feedback will appear here when submitted' : 'Try adjusting your filters to see more results' }}</p>
            </div>
            
            <div v-else>
              <!-- Feedback List -->
              <div class="space-y-4 h-[calc(60vh-200px)] overflow-y-auto custom-scrollbar">
                <div v-for="fb in paginatedFeedback" :key="fb.id" class="glass-item p-4 lg:p-5 rounded-lg hover:scale-[1.02] transition-all duration-300 touch-manipulation">
                  <div class="flex flex-col lg:flex-row lg:justify-between lg:items-start mb-3">
                    <div class="flex items-center space-x-3 mb-2 lg:mb-0">
                      <div class="w-8 lg:w-10 h-8 lg:h-10 bg-gradient-to-r from-blue-400 to-purple-500 rounded-full flex items-center justify-center">
                        <span class="text-white font-bold text-xs lg:text-sm">{{ fb.email.charAt(0).toUpperCase() }}</span>
                      </div>
                      <div>
                        <span class="font-semibold theme-text text-sm lg:text-base">{{ fb.email }}</span>
                        <div class="flex items-center space-x-2 mt-1">
                          <div class="flex">
                            <span v-for="i in 5" :key="i" 
                              :class="i <= fb.rating ? 'text-yellow-400' : 'text-gray-600'"
                              class="text-xs lg:text-sm">⭐</span>
                          </div>
                          <span class="text-xs px-2 py-1 rounded-full bg-gradient-to-r from-indigo-500 to-purple-600 text-white">
                            {{ availableCategories.find(cat => cat.value === fb.category)?.label || fb.category }}
                          </span>
                        </div>
                      </div>
                    </div>
                    <span class="text-xs theme-text-muted">{{ new Date(fb.created_at).toLocaleDateString() }}</span>
                  </div>
                  
                  <div class="mb-3">
                    <p class="theme-text leading-relaxed text-sm lg:text-base">{{ fb.message }}</p>
                  </div>
                  
                  <div v-if="fb.system_info" class="text-xs theme-text-muted bg-gray-800 bg-opacity-30 p-2 rounded border-l-2 border-blue-400">
                    <strong>System Info:</strong> {{ fb.system_info }}
                  </div>
                </div>
              </div>
              
              <!-- Pagination Controls -->
              <div v-if="totalPages > 1" class="mt-6 flex flex-col lg:flex-row justify-between items-center gap-4">
                <div class="text-sm theme-text-muted">
                  Page {{ feedbackPagination.currentPage }} of {{ totalPages }}
                </div>
                
                <div class="flex items-center space-x-2">
                  <!-- Previous Button -->
                  <button 
                    @click="previousPage"
                    :disabled="feedbackPagination.currentPage === 1"
                    class="px-3 py-2 glass-button text-white rounded-lg transition-all duration-300 hover:scale-105 disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 19l-7-7 7-7" />
                    </svg>
                  </button>
                  
                  <!-- Page Numbers -->
                  <div class="flex space-x-1">
                    <button 
                      v-for="page in Math.min(totalPages, 7)" 
                      :key="page"
                      @click="changePage(page)"
                      :class="[
                        'px-3 py-2 rounded-lg transition-all duration-300 hover:scale-105',
                        feedbackPagination.currentPage === page 
                          ? 'bg-gradient-to-r from-purple-500 to-pink-600 text-white' 
                          : 'glass-button text-white hover:bg-white/20'
                      ]"
                    >
                      {{ page }}
                    </button>
                  </div>
                  
                  <!-- Next Button -->
                  <button 
                    @click="nextPage"
                    :disabled="feedbackPagination.currentPage === totalPages"
                    class="px-3 py-2 glass-button text-white rounded-lg transition-all duration-300 hover:scale-105 disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 5l7 7-7 7" />
                    </svg>
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {      activeView: 'dashboard',
      isMobileSidebarOpen: false,
      analysisResult: null,
      feedbackList: [],
      // Feedback filtering and pagination
      feedbackFilters: {
        email: '',
        rating: '',
        dateFrom: '',
        dateTo: '',
        category: ''
      },
      feedbackPagination: {
        currentPage: 1,
        itemsPerPage: 10,
        totalItems: 0
      },
      backupHistory: [
        { id: 1, filename: 'backup_20250530_143022.json', date: '2025-05-30 14:30:22', size: '2.4 MB' },
        { id: 2, filename: 'backup_20250529_120015.json', date: '2025-05-29 12:00:15', size: '2.3 MB' },
        { id: 3, filename: 'backup_20250528_180045.json', date: '2025-05-28 18:00:45', size: '2.2 MB' }
      ]
    };
  },  computed: {
    filteredFeedback() {
      let filtered = [...this.feedbackList];
      
      // Filter by email
      if (this.feedbackFilters.email) {
        filtered = filtered.filter(fb => 
          fb.email.toLowerCase().includes(this.feedbackFilters.email.toLowerCase())
        );
      }
      
      // Filter by rating
      if (this.feedbackFilters.rating) {
        filtered = filtered.filter(fb => 
          fb.rating === parseInt(this.feedbackFilters.rating)
        );
      }
      
      // Filter by category
      if (this.feedbackFilters.category) {
        filtered = filtered.filter(fb => 
          fb.category === this.feedbackFilters.category
        );
      }
      
      // Filter by date range
      if (this.feedbackFilters.dateFrom || this.feedbackFilters.dateTo) {
        filtered = filtered.filter(fb => {
          const feedbackDate = new Date(fb.created_at);
          const fromDate = this.feedbackFilters.dateFrom ? new Date(this.feedbackFilters.dateFrom) : null;
          const toDate = this.feedbackFilters.dateTo ? new Date(this.feedbackFilters.dateTo + 'T23:59:59') : null;
          
          if (fromDate && feedbackDate < fromDate) return false;
          if (toDate && feedbackDate > toDate) return false;
          return true;
        });
      }
      
      return filtered;
    },
    
    totalItems() {
      return this.filteredFeedback.length;
    },
    
    paginatedFeedback() {
      const start = (this.feedbackPagination.currentPage - 1) * this.feedbackPagination.itemsPerPage;
      const end = start + this.feedbackPagination.itemsPerPage;
      return this.filteredFeedback.slice(start, end);
    },
    
    totalPages() {
      return Math.ceil(this.totalItems / this.feedbackPagination.itemsPerPage);
    },
    
    availableCategories() {
      return [
        { value: 'bug', label: '🐛 Bug Report' },
        { value: 'feature', label: '💡 Feature Request' },
        { value: 'ui', label: '🎨 UI/UX Feedback' },
        { value: 'performance', label: '⚡ Performance Issue' },
        { value: 'general', label: '💬 General Feedback' },
        { value: 'compliment', label: '👏 Compliment' }
      ];
    }
  },
  methods: {
    toggleMobileSidebar() {
      this.isMobileSidebarOpen = !this.isMobileSidebarOpen;
    },
    closeMobileSidebar() {
      this.isMobileSidebarOpen = false;
    },
    navigateToView(view) {
      this.activeView = view;
      this.closeMobileSidebar(); // Close sidebar on mobile after navigation
    },
    getCurrentViewName() {
      const viewNames = {
        dashboard: 'Dashboard',
        backup: 'Backup Data',
        analytics: 'Analytics',
        feedback: 'Feedback'
      };
      return viewNames[this.activeView] || 'Dashboard';
    },
    async fetchUserStatistics() {
      try {
        const response = await fetch('http://192.168.1.113/expenseNote/api/api.php?action=get_user_statistics');
        const result = await response.json();
        if (result.success) {
          document.getElementById('daily-users').textContent = result.data.daily;
          document.getElementById('weekly-users').textContent = result.data.weekly;
          document.getElementById('monthly-users').textContent = result.data.monthly;
          document.getElementById('yearly-users').textContent = result.data.yearly;
        } else {
          console.error('Error fetching user statistics:', result.message);
        }
      } catch (error) {
        console.error('Error fetching user statistics:', error);
      }
    },
    async backupUserData() {
      try {
        const button = event.target;
        const originalText = button.innerHTML;
        button.innerHTML = '<svg class="animate-spin h-4 lg:h-5 w-4 lg:w-5 inline mr-2" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24"><circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle><path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path></svg>Creating Backup...';
        button.disabled = true;

        const response = await fetch('http://192.168.1.113/expenseNote/api/api.php?action=backup_user_data');
        const result = await response.json();
        
        if (result.success) {
          const newBackup = {
            id: Date.now(),
            filename: result.file.split('/').pop(),
            date: new Date().toLocaleString(),
            size: 'Unknown'
          };
          this.backupHistory.unshift(newBackup);
          this.showNotification('Backup created successfully!', 'success');
        } else {
          this.showNotification(`Backup failed: ${result.message}`, 'error');
        }
        
        button.innerHTML = originalText;
        button.disabled = false;
      } catch (error) {
        console.error('Error backing up user data:', error);
        this.showNotification('Error creating backup', 'error');
      }
    },
    async analyzeUserData() {
      try {
        const button = event.target;
        const originalText = button.innerHTML;
        button.innerHTML = '<svg class="animate-spin h-4 lg:h-5 w-4 lg:w-5 inline mr-2" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24"><circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle><path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path></svg>Analyzing...';
        button.disabled = true;

        const response = await fetch('http://192.168.1.113/expenseNote/api/api.php?action=analyze_user_data');
        const result = await response.json();
        
        if (result.success) {
          this.analysisResult = result.data;
          this.showNotification('Analysis completed successfully!', 'success');
        } else {
          console.error('Error analyzing user data:', result.message);
          this.showNotification('Error analyzing data', 'error');
        }
        
        button.innerHTML = originalText;
        button.disabled = false;
      } catch (error) {
        console.error('Error analyzing user data:', error);
        this.showNotification('Error analyzing data', 'error');
      }
    },
    async fetchFeedback() {
      try {
        const response = await fetch('http://192.168.1.113/expenseNote/api/api.php?action=get_feedback_messages');
        const result = await response.json();
        if (result.success) {
          this.feedbackList = result.data;
          this.showNotification('Feedback refreshed successfully!', 'success');
        } else {
          this.feedbackList = [];
          this.showNotification('No feedback found', 'info');
        }      } catch (error) {
        this.feedbackList = [];
        console.error('Error fetching feedback:', error);
        this.showNotification('Error fetching feedback', 'error');
      }
    },
    
    // Feedback filtering and pagination methods
    clearFilters() {
      this.feedbackFilters = {
        email: '',
        rating: '',
        dateFrom: '',
        dateTo: '',
        category: ''
      };
      this.feedbackPagination.currentPage = 1;
    },
    
    changePage(page) {
      if (page >= 1 && page <= this.totalPages) {
        this.feedbackPagination.currentPage = page;
      }
    },
    
    previousPage() {
      if (this.feedbackPagination.currentPage > 1) {
        this.feedbackPagination.currentPage--;
      }
    },
    
    nextPage() {
      if (this.feedbackPagination.currentPage < this.totalPages) {
        this.feedbackPagination.currentPage++;
      }
    },
    
    onFilterChange() {
      this.feedbackPagination.currentPage = 1;
    },
    
    showNotification(message, type = 'info') {
      const colors = {
        success: 'bg-green-500',
        error: 'bg-red-500',
        info: 'bg-blue-500'
      };
      
      const notification = document.createElement('div');
      notification.className = `fixed top-4 right-4 ${colors[type]} text-white px-4 lg:px-6 py-2 lg:py-3 rounded-lg shadow-lg z-50 transition-all duration-300 text-sm lg:text-base`;
      notification.textContent = message;
      
      document.body.appendChild(notification);
      
      setTimeout(() => {
        notification.style.opacity = '0';
        setTimeout(() => {
          document.body.removeChild(notification);
        }, 300);
      }, 3000);
    }
  },
  mounted() {
    this.fetchUserStatistics();
    this.fetchFeedback();
    
    // Handle window resize
    window.addEventListener('resize', () => {
      if (window.innerWidth >= 1024) {
        this.isMobileSidebarOpen = false;
      }
    });
  },
  beforeUnmount() {
    window.removeEventListener('resize', () => {});
  },
};
</script>

<style scoped>
/* Glassmorphism base classes */
.glass-card {
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.2);
}

.glass-item {
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.08);
  box-shadow: 0 4px 24px 0 rgba(31, 38, 135, 0.15);
}

.glass-button {
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 16px 0 rgba(31, 38, 135, 0.3);
}

.glass-button:hover {
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  box-shadow: 0 6px 24px 0 rgba(31, 38, 135, 0.4);
}

.glass-sidebar {
  backdrop-filter: blur(25px);
  -webkit-backdrop-filter: blur(25px);
  background: rgba(255, 255, 255, 0.1);
  border-right: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 4px 0 32px 0 rgba(31, 38, 135, 0.3);
}

/* Sidebar Navigation */
.sidebar-item {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.05);
  color: rgba(255, 255, 255, 0.8);
}

.sidebar-item:hover {
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.95);
  transform: translateX(4px);
}

.sidebar-item-active {
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.3), rgba(147, 51, 234, 0.3));
  border: 1px solid rgba(59, 130, 246, 0.4);
  color: #ffffff;
  box-shadow: 0 4px 20px 0 rgba(59, 130, 246, 0.3);
}

.sidebar-item-active:hover {
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.4), rgba(147, 51, 234, 0.4));
  transform: translateX(4px);
}

/* Theme text classes */
.theme-text {
  color: #ffffff;
}

.theme-text-muted {
