<template>
  <form @submit.prevent="handleSubmit" class="glass-card p-6 rounded-lg shadow-lg">
    <h2 class="text-2xl font-semibold theme-text mb-6">{{ editingTransaction ? 'Edit' : 'Add New' }} Expense</h2>

    <!-- Basic Fields -->
    <div class="grid grid-cols-1 gap-6 mb-6">
      <input type="text" v-model="form.name" placeholder="Name" class="form-input" required>
    </div>
    
    <div class="grid grid-cols-1 gap-6 mb-6">
      <input type="number" v-model.number="form.price_mmk" placeholder="Price (MMK)" class="form-input"
        @input="convertToDollars" required>
      
      <select v-model="form.type" class="form-input" required>
        <option value="income">Income</option>
        <option value="outcome">Outcome</option>
      </select>
      
    </div>
    
    <!-- See More/Less Button -->
    <div class="mb-6" v-if="!editingTransaction">
      <button type="button" @click="toggleAdvancedFields"
      class="text-sm text-blue-400 hover:text-blue-300 transition-colors duration-200 flex items-center gap-2">
      <svg :class="{ 'rotate-180': showAdvancedFields }" class="w-4 h-4 transition-transform duration-200"
      fill="none" stroke="currentColor" viewBox="0 0 24 24">
      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M19 9l-7 7-7-7" />
    </svg>
    {{ showAdvancedFields ? 'See Less' : 'See More' }}
  </button>
</div>

<!-- Advanced Fields (conditionally shown) -->
<transition name="fade-slide">
      <div v-if="showAdvancedFields || editingTransaction" class="space-y-6 mb-6">

        <!-- Description field -->
        <div>
          <textarea v-model="form.description" placeholder="Description" class="form-input" rows="2"></textarea>
        </div>

        <!-- Date and Time fields -->
        <div class="grid grid-cols-1 md:grid-cols-2 gap-6">
          <input type="date" v-model="form.date" class="form-input" required>
          <input type="time" v-model="form.time" class="form-input" required>
        </div>
      </div>
    </transition>

    <!-- Submit Button -->
    <button type="submit" class="btn-primary w-full">{{ editingTransaction ? 'Update' : 'Add' }} Expense</button>
    
    <!-- Cancel Edit Button -->
    <button v-if="editingTransaction" @click="handleCancel" type="button" class="btn-secondary w-full mt-3">
      Cancel Edit
    </button>
  </form>
</template>

<script>
import { ref, reactive, watch } from 'vue';
import RecordRTC from 'recordrtc';
import Swal from 'sweetalert2';

export default {
  name: 'AddItem',
  props: {
    editingTransaction: {
      type: Object,
      default: null
    },
    exchangeRate: {
      type: Number,
      default: 4500
    }
  },
  emits: ['add-transaction', 'update-transaction', 'cancel-edit'],
  setup(props, { emit }) {
    // Voice Recording State
    const isRecording = ref(false);
    const isProcessing = ref(false);
    const recordingTime = ref(0);
    const lastTranscription = ref('');
    const voiceError = ref('');
    const recorder = ref(null);
    const recordingTimer = ref(null);
    const mediaStream = ref(null);

    // Form expansion state
    const showAdvancedFields = ref(false);    // Form data
    const form = reactive({
      id: null,
      name: '',
      type: 'outcome',
      price_usd: null,
      price_mmk: null,
      description: '',
      category: '',
      date: new Date().toISOString().slice(0, 10),
      time: new Date().toTimeString().slice(0, 5),
    });

    // Form Methods (defined before watch to avoid initialization errors)
    const resetForm = () => {
      form.id = null;
      form.name = '';
      form.type = 'outcome';
      form.price_usd = null;
      form.price_mmk = null;
      form.description = '';
      form.category = '';
      form.date = new Date().toISOString().slice(0, 10);
      form.time = new Date().toTimeString().slice(0, 5);      showAdvancedFields.value = false;
    };

    // Watch for editing transaction changes (defined after resetForm to avoid initialization errors)
    watch(() => props.editingTransaction, (newVal) => {
      if (newVal) {
        form.id = newVal.id;
        form.name = newVal.name;
        form.type = newVal.type;
        form.price_usd = parseFloat(newVal.price_usd);
        form.price_mmk = parseFloat(newVal.price_mmk);
        form.description = newVal.description;
        form.category = newVal.category;
        form.date = newVal.date;
        form.time = newVal.time;
      } else {
        resetForm();
      }
    }, { immediate: true });

    const handleSubmit = () => {
      if (!form.name || form.price_mmk === null || !form.date || !form.time) {
        Swal.fire({
          icon: 'warning',
          title: 'Missing Information',
          text: 'Please fill in all required fields.',
          confirmButtonColor: '#3b82f6'
        });
        return;
      }

      const transactionData = { ...form };
      
      if (props.editingTransaction) {
        emit('update-transaction', transactionData);
      } else {
        emit('add-transaction', transactionData);
        resetForm();
      }
    };

    const handleCancel = () => {
      emit('cancel-edit');
      resetForm();
    };

    const convertToKyats = () => {
      if (form.price_usd !== null && form.price_usd !== '') {
        form.price_mmk = parseFloat(form.price_usd) * props.exchangeRate;
      } else if (form.price_usd === '') {
        form.price_mmk = null;
      }
    };

    const convertToDollars = () => {
      if (form.price_mmk !== null && form.price_mmk !== '') {
        form.price_usd = parseFloat(form.price_mmk) / props.exchangeRate;
      } else if (form.price_mmk === '') {
        form.price_usd = null;
      }
    };

    const toggleAdvancedFields = () => {
      showAdvancedFields.value = !showAdvancedFields.value;
    };

    // Voice Recording Functions
    const toggleRecording = async () => {
      if (isRecording.value) {
        stopRecording();
      } else {
        await startRecording();
      }
    };

    const startRecording = async () => {
      try {
        voiceError.value = '';

        // Request microphone access
        const stream = await navigator.mediaDevices.getUserMedia({
          audio: {
            echoCancellation: true,
            noiseSuppression: true,
            sampleRate: 44100
          }
        });

        mediaStream.value = stream;

        // Create recorder
        recorder.value = new RecordRTC(stream, {
          type: 'audio',
          mimeType: 'audio/wav',
          recorderType: RecordRTC.StereoAudioRecorder,
          numberOfAudioChannels: 1,
          desiredSampRate: 16000,
          bufferSize: 4096
        });

        recorder.value.startRecording();
        isRecording.value = true;
        recordingTime.value = 0;

        // Start timer
        recordingTimer.value = setInterval(() => {
          recordingTime.value++;
          if (recordingTime.value >= 60) { // Auto-stop after 60 seconds
            stopRecording();
          }
        }, 1000);

      } catch (error) {
        voiceError.value = 'Microphone access denied. Please enable microphone permissions.';
        console.error('Error starting recording:', error);
      }
    };

    const stopRecording = () => {
      if (!recorder.value || !isRecording.value) return;

      isRecording.value = false;
      isProcessing.value = true;

      // Clear timer
      if (recordingTimer.value) {
        clearInterval(recordingTimer.value);
        recordingTimer.value = null;
      }

      recorder.value.stopRecording(() => {
        const blob = recorder.value.getBlob();

        // Stop media stream
        if (mediaStream.value) {
          mediaStream.value.getTracks().forEach(track => track.stop());
        }

        // Process the audio blob
        processAudioBlob(blob);
      });
    };

    const processAudioBlob = async (blob) => {
      try {
        // For now, we'll use the Web Speech API as fallback
        // In a real implementation, you would send this to a Whisper API

        // Convert blob to audio URL for future Whisper.cpp integration
        // const audioUrl = URL.createObjectURL(blob);
        // TODO: Send blob to Whisper.cpp API when implemented
        console.log('Audio blob ready for processing:', blob); // For debugging and future Whisper integration

        // Simulate Whisper processing with Web Speech API
        await processWithWebSpeech();

      } catch (error) {
        voiceError.value = 'Error processing audio. Please try again.';
        console.error('Error processing audio:', error);
        isProcessing.value = false;
      }
    };

    const processWithWebSpeech = () => {
      return new Promise((resolve, reject) => {
        if (!('webkitSpeechRecognition' in window) && !('SpeechRecognition' in window)) {
          // Fallback - simulate transcription for demo
          setTimeout(() => {
            lastTranscription.value = "Coffee expense 5 dollars outcome daily coffee purchase";
            isProcessing.value = false;
            resolve();
          }, 2000);
          return;
        }

        const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
        const recognition = new SpeechRecognition();

        recognition.continuous = false;
        recognition.interimResults = false;
        recognition.lang = 'en-US';

        recognition.onresult = (event) => {
          const transcript = event.results[0][0].transcript;
          lastTranscription.value = transcript;
          isProcessing.value = false;
          resolve();
        };

        recognition.onerror = (event) => {
          voiceError.value = 'Speech recognition error. Please try again.';
          isProcessing.value = false;
          reject(event.error);
        };

        recognition.onend = () => {
          if (isProcessing.value) {
            isProcessing.value = false;
            voiceError.value = 'No speech detected. Please try again.';
          }
        };

        // Start a new recording for speech recognition
        navigator.mediaDevices.getUserMedia({ audio: true })
          .then(stream => {
            recognition.start();
            setTimeout(() => {
              stream.getTracks().forEach(track => track.stop());
            }, 100);
          })
          .catch(error => {
            voiceError.value = 'Could not access microphone for speech recognition.';
            isProcessing.value = false;
            reject(error);
          });
      });
    };

    const applyTranscription = () => {
      if (!lastTranscription.value) return;

      try {
        const text = lastTranscription.value.toLowerCase();

        // Parse the transcription for transaction data
        parseTranscriptionToForm(text);

        Swal.fire({
          icon: 'success',
          title: 'Voice Input Applied',
          text: 'Transaction details have been filled from your voice input.',
          confirmButtonColor: '#3b82f6'
        });

      } catch (error) {
        Swal.fire({
          icon: 'warning',
          title: 'Parsing Error',
          text: 'Could not parse all details from voice input. Please check and complete the form.',
          confirmButtonColor: '#3b82f6'
        });
      }
    };

    const parseTranscriptionToForm = (text) => {
      // Simple parsing logic - can be enhanced with NLP

      // Extract amount (look for numbers followed by dollar/USD)
      const amountMatch = text.match(/(\d+(?:\.\d{2})?)\s*(?:dollar|usd|bucks?)/i);
      if (amountMatch) {
        form.price_usd = parseFloat(amountMatch[1]);
        form.price_mmk = form.price_usd * props.exchangeRate;
      }

      // Extract transaction type
      if (text.includes('income') || text.includes('salary') || text.includes('earning')) {
        form.type = 'income';
      } else if (text.includes('expense') || text.includes('spent') || text.includes('bought') || text.includes('purchase')) {
        form.type = 'outcome';
      }

      // Extract name/description (basic keyword matching)
      const keywords = ['coffee', 'lunch', 'dinner', 'gas', 'groceries', 'shopping', 'taxi', 'uber', 'salary', 'bonus'];
      for (const keyword of keywords) {
        if (text.includes(keyword)) {
          form.name = keyword.charAt(0).toUpperCase() + keyword.slice(1);
          form.category = getCategoryFromKeyword(keyword);
          break;
        }
      }

      // Set description
      form.description = lastTranscription.value;

      // Set current date and time if not already set
      if (!form.date) {
        form.date = new Date().toISOString().slice(0, 10);
      }
      if (!form.time) {
        form.time = new Date().toTimeString().slice(0, 5);
      }
    };

    const getCategoryFromKeyword = (keyword) => {
      const categoryMap = {
        'coffee': 'Food & Drink',
        'lunch': 'Food & Drink',
        'dinner': 'Food & Drink',
        'groceries': 'Food & Drink',
        'gas': 'Transportation',
        'taxi': 'Transportation',
        'uber': 'Transportation',
        'shopping': 'Shopping',
        'salary': 'Income',
        'bonus': 'Income'
      };
      return categoryMap[keyword] || 'Other';
    };

    return {
      form,
      showAdvancedFields,
      isRecording,
      isProcessing,
      recordingTime,
      lastTranscription,
      voiceError,
      handleSubmit,
      handleCancel,
      convertToKyats,
      convertToDollars,
      toggleAdvancedFields,
      toggleRecording,
      applyTranscription
    };
  }
};
</script>

<style scoped>
/* Advanced Fields Animation */
.fade-slide-enter-active,
.fade-slide-leave-active {
  transition: all 0.3s ease;
}

.fade-slide-enter-from {
  opacity: 0;
  transform: translateY(-10px);
}

.fade-slide-leave-to {
  opacity: 0;
  transform: translateY(-10px);
}

/* See More Button Styles */
.rotate-180 {
  transform: rotate(180deg);
}

/* Voice Recording Styles */
.voice-btn {
  min-width: 140px;
  font-weight: 500;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.voice-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.15);
}

.voice-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.recording-pulse {
  animation: pulse 1.5s ease-in-out infinite;
}

@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.05);
    opacity: 0.8;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}


.theme-light .form {
  color: #ffffff;
  font-weight: 600;
  padding: 12px 24px;
  border-radius: 8px;
  box-shadow: 0 4px 16px 0 rgba(236, 72, 153, 0.3);
  transition: all 0.3s ease;
  border: none;
  cursor: pointer;
}

.btn-primary {
  background: linear-gradient(135deg, #ec4899 0%, #be185d 100%);
  color: #ffffff;
  font-weight: 600;
  padding: 12px 24px;
  border-radius: 8px;
  box-shadow: 0 4px 16px 0 rgba(236, 72, 153, 0.3);
  transition: all 0.3s ease;
  border: none;
  cursor: pointer;
}

.btn-primary:hover {
  background: linear-gradient(135deg, #be185d 0%, #9d174d 100%);
  transform: translateY(-2px);
  box-shadow: 0 6px 24px 0 rgba(236, 72, 153, 0.4);
}

.btn-secondary {
  background: linear-gradient(135deg, #6b7280 0%, #4b5563 100%);
  color: #ffffff;
  font-weight: 300;
  padding: 4px 8px;
  border-radius: 8px;
  box-shadow: 0 4px 16px 0 rgba(107, 114, 128, 0.3);
  transition: all 0.3s ease;
  border: none;
  cursor: pointer;
  font-size: 12px;
}

.btn-secondary:hover {
  background: linear-gradient(135deg, #4b5563 0%, #374151 100%);
  transform: translateY(-2px);
  box-shadow: 0 6px 24px 0 rgba(107, 114, 128, 0.4);
}

.btn-export {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  color: #ffffff;
  font-weight: 600;
  padding: 10px 20px;
  border-radius: 8px;
  box-shadow: 0 4px 16px 0 rgba(16, 185, 129, 0.3);
  transition: all 0.3s ease;
  border: none;
  cursor: pointer;
  font-size: 14px;
}

.btn-export:hover:not(.opacity-50) {
  background: linear-gradient(135deg, #059669 0%, #047857 100%);
  transform: translateY(-2px);
  box-shadow: 0 6px 24px 0 rgba(16, 185, 129, 0.4);
}

.btn-export:disabled {
  background: linear-gradient(135deg, #6b7280 0%, #4b5563 100%);
  box-shadow: none;
  cursor: not-allowed;
}

.btn-icon {
  padding: 8px;
  border-radius: 50%;
  transition: all 0.3s ease;
  border: none;
  cursor: pointer;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.theme-dark .btn-icon:hover {
  background: rgba(255, 255, 255, 0.1);
}

.theme-light .btn-icon:hover {
  background: rgba(255, 255, 255, 0.3);
}

.transaction-item {
  transition: all 0.3s ease;
}

.transaction-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.3);
}

.form-input {
  width: 100%;
  padding: 12px 16px;
  border-radius: 8px;
  border: none;
  outline: none;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.theme-dark .form-input {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
}

.theme-light .form-input {
  background: rgba(255, 255, 255, 0.4);
  color: #1f2937;
}

.theme-dark .form-input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.theme-light .form-input::placeholder {
  color: rgba(31, 41, 55, 0.5);
}

.form-input:focus {
  border: 2px solid #ec4899;
  outline: none;
  transform: translateY(-2px);
  box-shadow: 0 4px 20px 0 rgba(236, 72, 153, 0.3);
}

.theme-dark .form-input:focus {
  background: rgba(255, 255, 255, 0.15);
}
.transcription-box {
  background: rgba(34, 197, 94, 0.1);
  border: 1px solid rgba(34, 197, 94, 0.3);
  backdrop-filter: blur(10px);
}

.theme-dark .transcription-box {
  background: rgba(34, 197, 94, 0.15);
  border: 1px solid rgba(34, 197, 94, 0.4);
}

.error-box {
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.3);
  backdrop-filter: blur(10px);
}

.theme-dark .error-box {
  background: rgba(239, 68, 68, 0.15);
  border: 1px solid rgba(239, 68, 68, 0.4);
}

.theme-dark .theme-text {
  color: #ffffff;
}

.theme-light .theme-text {
  color: #1f2937;
}

.glass-card {
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 24px 0 rgba(31, 38, 135, 0.2);
}

.theme-dark .glass-card {
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.theme-light .glass-card {
  background: rgba(255, 255, 255, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.3);
}
</style>
