<template>
  <!-- Financial Summary Modal -->
  <div v-if="show" class="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center z-50 p-4" @click.self="$emit('close')">
    <div class="glass-modal max-w-md w-full max-h-[90vh] overflow-y-auto animate-modalSlideIn">
      <div class="flex justify-between items-center mb-6">
        <h2 class="text-2xl font-bold bg-gradient-to-r from-green-500 to-emerald-500 bg-clip-text text-transparent">💰 Financial Summary</h2>
        <button @click="$emit('close')" class="glass-button p-2 rounded-lg hover:scale-110 transition-all group">
          <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 text-gray-500 group-hover:text-red-500 transition-colors" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M6 18L18 6M6 6l12 12" />
          </svg>
        </button>
      </div>

      <div class="space-y-4">
        <!-- Balance Cards -->
        <div class="grid grid-cols-1 gap-4">
          <!-- Total Income -->
          <div class="glass-item p-4 rounded-xl border-l-4 border-green-400 hover:scale-105 transition-all duration-300">
            <div class="flex items-center justify-between">
              <div>
                <p class="text-sm text-gray-600 dark:text-gray-300">Total Income</p>
                <p class="text-2xl font-bold text-green-500">{{ (totalIncome * exchangeRate).toLocaleString() }} MMK</p>
                <p class="text-sm text-gray-500">${{ totalIncome.toFixed(2) }}</p>
              </div>
              <div class="w-12 h-12 bg-green-100 dark:bg-green-900 rounded-full flex items-center justify-center">
                <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 text-green-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M7 11l5-5m0 0l5 5m-5-5v12" />
                </svg>
              </div>
            </div>
          </div>

          <!-- Total Expenses -->
          <div class="glass-item p-4 rounded-xl border-l-4 border-red-400 hover:scale-105 transition-all duration-300">
            <div class="flex items-center justify-between">
              <div>
                <p class="text-sm text-gray-600 dark:text-gray-300">Total Expenses</p>
                <p class="text-2xl font-bold text-red-500">{{ (totalExpenses * exchangeRate).toLocaleString() }} MMK</p>
                <p class="text-sm text-gray-500">${{ totalExpenses.toFixed(2) }}</p>
              </div>
              <div class="w-12 h-12 bg-red-100 dark:bg-red-900 rounded-full flex items-center justify-center">
                <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6 text-red-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M17 13l-5 5m0 0l-5-5m5 5V6" />
                </svg>
              </div>
            </div>
          </div>

          <!-- Net Balance -->
          <div class="glass-item p-4 rounded-xl border-l-4 hover:scale-105 transition-all duration-300"
            :class="netBalance >= 0 ? 'border-blue-400' : 'border-orange-400'">
            <div class="flex items-center justify-between">
              <div>
                <p class="text-sm text-gray-600 dark:text-gray-300">Net Balance</p>
                <p class="text-2xl font-bold" :class="netBalance >= 0 ? 'text-blue-500' : 'text-orange-500'">
                  {{ (netBalance * exchangeRate).toLocaleString() }} MMK
                </p>
                <p class="text-sm text-gray-500">${{ netBalance.toFixed(2) }}</p>
              </div>
              <div class="w-12 h-12 rounded-full flex items-center justify-center"
                :class="netBalance >= 0 ? 'bg-blue-100 dark:bg-blue-900' : 'bg-orange-100 dark:bg-orange-900'">
                <svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" 
                  :class="netBalance >= 0 ? 'text-blue-600' : 'text-orange-600'" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 8c-1.657 0-3 .895-3 2s1.343 2 3 2 3 .895 3 2-1.343 2-3 2m0-8c1.11 0 2.08.402 2.599 1M12 8V7m0 1v8m0 0v1m0-1c-1.11 0-2.08-.402-2.599-1M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                </svg>
              </div>
            </div>
          </div>
        </div>

        <!-- Stats Grid -->
        <div class="grid grid-cols-2 gap-4 mt-6">
          <div class="glass-item p-3 rounded-lg text-center">
            <p class="text-xs text-gray-600 dark:text-gray-400">Transactions</p>
            <p class="text-lg font-bold text-gray-800 dark:text-white">{{ totalTransactions }}</p>
          </div>          <div class="glass-item p-3 rounded-lg text-center">
            <p class="text-xs text-gray-600 dark:text-gray-400">Avg. Amount</p>
            <p class="text-lg font-bold text-gray-800 dark:text-white">{{ (avgTransactionAmount * exchangeRate).toLocaleString() }} MMK</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'FinancialSummaryModal',
  props: {
    show: {
      type: Boolean,
      default: false
    },
    totalIncome: {
      type: Number,
      default: 0
    },
    totalExpenses: {
      type: Number,
      default: 0
    },
    netBalance: {
      type: Number,
      default: 0
    },
    exchangeRate: {
      type: Number,
      default: 4500
    },
    transactions: {
      type: Array,
      default: () => []
    }
  },
  computed: {
    totalTransactions() {
      return this.transactions.length;
    },
    avgTransactionAmount() {
      if (this.transactions.length === 0) return 0;
      const total = this.transactions.reduce((sum, t) => sum + (parseFloat(t.price_usd) || 0), 0);
      return total / this.transactions.length;
    }
  },
  emits: ['close']
};
</script>

<style scoped>
.glass-modal {
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 24px;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
}

.glass-button {
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 2px 12px 0 rgba(31, 38, 135, 0.2);
}

.glass-item {
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.08);
}

.animate-modalSlideIn {
  animation: modalSlideIn 0.3s ease-out;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}
</style>
