<template>
  <!-- History Modal -->
  <div v-if="show" class="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center z-50 p-4" @click.self="$emit('close')">
    <div class="glass-modal max-w-lg w-full max-h-[90vh] overflow-y-auto animate-modalSlideIn">
      <div class="flex justify-between items-center mb-6">
        <h2 class="text-2xl font-bold bg-gradient-to-r from-purple-500 to-pink-500 bg-clip-text text-transparent">🗑️ Deleted Items</h2>
        <button @click="$emit('close')" class="glass-button p-2 rounded-lg hover:scale-110 transition-all group">
          <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 text-gray-500 group-hover:text-red-500 transition-colors" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M6 18L18 6M6 6l12 12" />
          </svg>
        </button>
      </div>
      
      <div v-if="deletedItems.length === 0" class="text-center py-8">
        <div class="w-16 h-16 bg-gray-100 dark:bg-gray-800 rounded-full flex items-center justify-center mx-auto mb-4">
          <svg xmlns="http://www.w3.org/2000/svg" class="h-8 w-8 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
          </svg>
        </div>
        <p class="text-gray-600 dark:text-gray-300 mb-2">No deleted items found</p>
        <p class="text-sm text-gray-500 dark:text-gray-400">When you delete transactions, they'll appear here for recovery</p>
      </div>

      <div v-else class="space-y-3">        <div v-for="(item, index) in deletedItems" :key="index" 
          class="glass-item p-4 rounded-lg hover:scale-[1.02] transition-all duration-300">
          <div class="flex justify-between items-center">
            <div class="flex-1">
              <p class="font-medium text-gray-800 dark:text-white">{{ item.name }}</p>
              <p class="text-sm text-gray-600 dark:text-gray-300 mt-1">{{ formatDate(item.date) }}</p>
              <p v-if="item.description" class="text-xs text-gray-500 dark:text-gray-400 mt-1">{{ item.description }}</p>
              <div class="flex items-center mt-2">
                <span class="px-2 py-1 text-xs rounded-full" 
                  :class="item.type === 'income' ? 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200' : 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-200'">
                  {{ item.type === 'income' ? '💰 Income' : '💸 Expense' }}
                </span>
                <span v-if="item.category" class="ml-2 px-2 py-1 text-xs rounded-full bg-gray-100 text-gray-800 dark:bg-gray-700 dark:text-gray-200">
                  {{ item.category }}
                </span>
              </div>
            </div>            <div class="text-right ml-4">
              <p class="text-lg font-bold" :class="item.type === 'income' ? 'text-green-500' : 'text-red-500'">
                {{ ((item.price_usd || 0) * exchangeRate).toLocaleString() }} MMK
              </p>
              <p class="text-sm text-gray-500">${{ (item.price_usd || 0).toFixed(2) }}</p>
              <p class="text-xs text-gray-400">Deleted: {{ formatDate(item.deleted_at) }}</p>
            </div>
          </div>
            <div class="flex justify-end space-x-2 mt-3">
            <button @click="$emit('restore-item', item)" 
              class="px-3 py-1 bg-blue-500 hover:bg-blue-600 text-white rounded transition-colors flex items-center text-sm">
              <svg xmlns="http://www.w3.org/2000/svg" class="h-4 w-4 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M3 10h10a8 8 0 018 8v2M3 10l6 6m-6-6l6-6" />
              </svg>
              Restore
            </button>
            <button @click="$emit('permanently-delete', item)" 
              class="px-3 py-1 bg-red-500 hover:bg-red-600 text-white rounded transition-colors">
              Delete Permanently
            </button>
          </div>
        </div>
        
        <p v-if="deletedItems.length === 0" class="text-center theme-text-muted py-4">
          No deleted items found in history
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HistoryModal',
  props: {
    show: {
      type: Boolean,
      default: false
    },
    deletedItems: {
      type: Array,
      default: () => []
    },
    exchangeRate: {
      type: Number,
      default: 4500
    }
  },
  methods: {
    formatDate(dateString) {
      if (!dateString) return '';
      const options = { year: 'numeric', month: 'long', day: 'numeric' };
      return new Date(dateString).toLocaleDateString(undefined, options);
    }
  },
  emits: ['close', 'restore-item', 'permanently-delete']
};
</script>

<style scoped>
.glass-modal {
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 24px;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
}

.glass-button {
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 2px 12px 0 rgba(31, 38, 135, 0.2);
}

.glass-item {
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.08);
}

.animate-modalSlideIn {
  animation: modalSlideIn 0.3s ease-out;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.theme-text-muted {
  color: rgba(255, 255, 255, 0.7);
}
</style>
