<template>
  <div class="flex items-center space-x-2">
    <!-- Export Range Selector -->
    <select v-model="exportRange" class="form-input w-auto py-2 px-3 text-sm">
      <option value="daily">Daily</option>
      <option value="weekly">Weekly</option>
      <option value="monthly">Monthly</option>
      <option value="yearly">Yearly</option>
    </select>
    
    <!-- Export Button -->
    <button 
      @click="exportToPDF" 
      :disabled="disabled || isExporting"
      class="btn-export flex items-center space-x-2"
      :class="{ 'opacity-50 cursor-not-allowed': disabled || isExporting }"
    >
      <svg v-if="!isExporting" xmlns="http://www.w3.org/2000/svg" class="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2"
          d="M12 10v6m0 0l-3-3m3 3l3-3m2 8H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
      </svg>
      <svg v-else class="animate-spin h-5 w-5" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
        <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
        <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
      </svg>
      <span class="hidden md:inline-flex">
        {{ isExporting ? 'Generating...' : 'Export PDF' }}
      </span>
    </button>
  </div>
</template>

<script>
import { ref } from 'vue';
import Swal from 'sweetalert2';

export default {
  name: 'ExportPDF',
  props: {
    disabled: {
      type: Boolean,
      default: false
    },
    transactions: {
      type: Array,
      default: () => []
    }
  },
  emits: ['export-started', 'export-completed', 'export-error'],
  setup(props, { emit }) {
    const exportRange = ref('weekly');
    const isExporting = ref(false);

    const exportToPDF = async () => {
      if (props.transactions.length === 0) {
        Swal.fire({
          icon: 'info',
          title: 'No Data',
          text: 'No transactions to export!',
          confirmButtonColor: '#3b82f6'
        });
        return;
      }

      if (isExporting.value) return;

      try {
        isExporting.value = true;
        emit('export-started');

        // Calculate date range based on exportRange
        let startDate, endDate;
        const today = new Date();
        
        if (exportRange.value === 'daily') {
          startDate = new Date(today);
          endDate = new Date(today);
        } else if (exportRange.value === 'weekly') {
          const day = today.getDay();
          startDate = new Date(today);
          startDate.setDate(today.getDate() - day + (day === 0 ? -6 : 1)); // Monday
          endDate = new Date(startDate);
          endDate.setDate(startDate.getDate() + 6); // Sunday
        } else if (exportRange.value === 'monthly') {
          startDate = new Date(today.getFullYear(), today.getMonth(), 1);
          endDate = new Date(today.getFullYear(), today.getMonth() + 1, 0);
        } else if (exportRange.value === 'yearly') {
          startDate = new Date(today.getFullYear(), 0, 1);
          endDate = new Date(today.getFullYear(), 11, 31);
        }

        // Format dates as yyyy-mm-dd
        const format = d => d.toISOString().slice(0, 10);
        const params = exportRange.value === 'daily'
          ? `?export_type=daily`
          : `?range=${exportRange.value}&start=${format(startDate)}&end=${format(endDate)}`;

        const response = await fetch(`http://192.168.1.113/expenseNote/api/export_pdf.php${params}`, {
          method: 'GET',
          headers: {
            'Accept': 'application/pdf',
          },
        });

        if (!response.ok) {
          // Try to parse JSON error from backend
          let errorMsg = `HTTP error! status: ${response.status}`;
          try {
            const text = await response.text();
            const json = JSON.parse(text);
            if (json && json.error) {
              errorMsg = json.error;
            }
          } catch (e) { 
            // Intentionally empty: fallback to default error message
          }
          
          Swal.fire({
            icon: 'info',
            title: 'No Data',
            text: errorMsg,
            confirmButtonColor: '#3b82f6'
          });
          
          emit('export-error', errorMsg);
          return;
        }

        const blob = await response.blob();
        const url = window.URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = url;
        
        // Generate filename based on export range
        const dateStr = today.toISOString().slice(0, 10);
        let filename = `expense-tracker-${exportRange.value}-${dateStr}.pdf`;
        
        if (exportRange.value !== 'daily') {
          const startStr = format(startDate);
          const endStr = format(endDate);
          filename = `expense-tracker-${exportRange.value}-${startStr}-to-${endStr}.pdf`;
        }
        
        link.download = filename;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        window.URL.revokeObjectURL(url);

        // Show success message
        Swal.fire({
          icon: 'success',
          title: 'Export Successful!',
          text: `PDF exported successfully for ${exportRange.value} period.`,
          confirmButtonColor: '#3b82f6',
          timer: 3000,
          timerProgressBar: true
        });

        emit('export-completed', { 
          range: exportRange.value,
          filename: filename,
          startDate: startDate,
          endDate: endDate
        });

      } catch (error) {
        console.error('Error exporting PDF:', error);
        
        Swal.fire({
          icon: 'error',
          title: 'Export Failed',
          text: 'Failed to export PDF. Please check your connection and try again.',
          confirmButtonColor: '#3b82f6'
        });

        emit('export-error', error.message);
      } finally {
        isExporting.value = false;
      }
    };

    return {
      exportRange,
      isExporting,
      exportToPDF
    };
  }
};
</script>

<style scoped>
.btn-export {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 8px 16px;
  border-radius: 8px;
  border: none;
  font-weight: 500;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

.btn-export:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.4);
}

.btn-export:disabled {
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.btn-export:active:not(:disabled) {
  transform: translateY(0);
}


.form-input {
  width: 100%;
  padding: 12px 16px;
  border-radius: 8px;
  border: none;
  outline: none;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.theme-dark .form-input {
  background: rgba(255, 255, 255, 0.1);
  color: #ffffff;
}

.theme-light .form-input {
  background: rgba(255, 255, 255, 0.4);
  color: #1f2937;
}

.theme-dark .form-input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.theme-light .form-input::placeholder {
  color: rgba(31, 41, 55, 0.5);
}

.form-input:focus {
  border: 2px solid #ec4899;
  outline: none;
  transform: translateY(-2px);
  box-shadow: 0 4px 20px 0 rgba(236, 72, 153, 0.3);
}

.theme-dark .form-input:focus {
  background: rgba(255, 255, 255, 0.15);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .btn-export {
    padding: 6px 12px;
    min-width: auto;
  }
}
</style>
